import {Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {CeSlateInterface} from '../../models/interfaces/ce-slate.interface';

@Component({
  selector: 'app-ce-slate-card',
  templateUrl: './ce-slate-card.component.html',
  styleUrls: ['./ce-slate-card.component.sass']
})
export class CeSlateCardComponent implements OnInit, OnChanges {
  @Input() data: CeSlateInterface;

  constructor() {
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.data) {
      this.data = changes.data.currentValue;
    }
  }

  dotDotDot(s: string) {
    if (s) {
      return s.length > 150 ? s.substr(0, 150) + '...' : s;
    }
    return '';
  }

  formatStatus(status){
    return status.replace(/\_/g," ");
  }

}
