import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {CommentsInterface, CommentsService} from '../../services/comments.service';
import {MessagingService} from '../../services/messaging.service';
import {UserService} from '../../services/user.service';

@Component({
  selector: 'app-comment-wrapper',
  templateUrl: './comment-wrapper.component.html',
  styleUrls: ['./comment-wrapper.component.sass']
})
export class CommentWrapperComponent implements OnInit {
  storyId: any;
  comments: Partial<CommentsInterface>[] = [];
  loading = true;
  @Input() type: any = 'story';
  @Input() id: any;
  @Input() story_id: any;
  @Input() discussions: any;
  @Output() commentCount: EventEmitter<number> = new EventEmitter<number>();


  constructor(private route: ActivatedRoute, private router: Router, private commentsService: CommentsService, private messagingService: MessagingService, private  user: UserService) {
    this.messagingService.currentMessage.subscribe(msg => {
      // @ts-ignore
      if (msg) {
        if (msg.notification.body.includes('commented')) {
          console.log(this.comments);
          // @ts-ignore
          let comment = msg.notification.comment;
          if (this.user.getUserData().id !== comment.user.id) {
            if (comment.parent_comment_id) {
              this.comments.forEach(commentData => {
                if (comment.parent_comment_id === commentData.id) {
                  commentData['child'].data.splice(0, 0, comment);
                }
              });
            } else {
              this.comments.splice(0, 0, comment);
              console.log(this.comments);
            }
          } else {
            if (this.comments[0].id !== comment.id) {
              if (comment.parent_comment_id) {
                this.comments.forEach(commentData => {
                  if (comment.parent_comment_id === commentData.id) {
                    commentData['child'].data.splice(0, 0, comment);
                  }
                });
              } else {
                this.comments.splice(0, 0, comment);
                console.log(this.comments);
              }

            }
          }
        }
      }
    });
    this.route.params.subscribe(params => {
      params.id ? this.storyId = params.id : (this.router.url.split('story/')[1] ? this.storyId = this.router.url.split('story/')[1] : this.storyId = this.story_id);

      this.commentsService.commentSub.subscribe(res => {
        if (res[0] != undefined) {
          this.commentCount.emit(res.length);
          this.loading = false;
          if (res[0].id < res[res.length - 1].id) {
            this.comments = [...res.reverse()];
          } else {
            this.comments = [...res];
          }
        }
      });
    });
  }

  ngOnInit() {
    if (!this.storyId && this.story_id) {
      this.storyId = this.story_id;
    }
    this.commentsService.getComments(({commentable_id: this.id ? this.id : this.storyId, commentable_type: this.type}), this.storyId);
  }

  addFiles(event) {

  }

  saveData() {

  }
}
