import {Injectable} from '@angular/core';
import {Constants} from '../utils/utils';
import {HttpClient} from '@angular/common/http';
import {HttpResponseInterface} from '../models/interfaces/http-response.interface';

@Injectable({
  providedIn: 'root'
})
export class TokenService {
  public static SESSION_STORAGE_KEY = 'accessToken';

  constructor(private http: HttpClient) {
  }

  getToken() {
    return localStorage.getItem(TokenService.SESSION_STORAGE_KEY);
  }

  storeToken(token) {
    localStorage.setItem(TokenService.SESSION_STORAGE_KEY, token);
  }

  refreshToken() {
    return this.http.get<HttpResponseInterface<{ token: string }>>(Constants.host + 'refresh');
  }
}
