/* tslint:disable:max-line-length no-output-native */
import {
  Directive,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  OnChanges,
  OnDestroy,
  Output,
  Renderer2,
  SimpleChanges
} from '@angular/core';
import {FileUploadStatusInterface, UploadService, ProfileImageInterface} from '../services/upload.service';
import {Observable, Subscription} from 'rxjs';
// import { ProcessEnvOptions } from 'child_process';
import {SubSink} from 'subsink';

@Directive({
  selector: '[appUpload]'
})
export class UploadDirective implements OnChanges, OnDestroy {
  @Input() multiple = false;
  @Output() progress: EventEmitter<FileUploadStatusInterface[]> = new EventEmitter<FileUploadStatusInterface[]>();
  @Output() profileprogress: EventEmitter<ProfileImageInterface> = new EventEmitter<ProfileImageInterface>();
  @Input() mimeType: 'audio/*' | 'video/*' | 'image/*' | string;
  @Input() page: 'profile' | 'others';
  input: HTMLElement = this.r2.createElement('input');
  file: Set<File>;
  private subs = new Subscription();

  @HostListener('mouseup', ['$event']) onClick(e) {
    if (e.button === 0) {
      this.input.click();
    }
    // this.highlight(null);
  }


  constructor(private el: ElementRef, private r2: Renderer2, private uploadService: UploadService) {
    this.input.setAttribute('hidden', 'true');
    this.input.setAttribute('type', 'file');
    this.r2.appendChild(this.el.nativeElement, this.input);
    this.input.addEventListener('change', (event) => this.fileSelected(event));
  }

  ngOnChanges(changes: SimpleChanges): void {

    if (changes.multiple) {
      this.multiple = changes.multiple.currentValue;
      if (this.multiple) {
        this.input.setAttribute('multiple', 'true');
      } else {
        this.input.removeAttribute('multiple');
      }
    }
    if (changes.mimeType) {
      this.mimeType = changes.mimeType.currentValue;
      if (this.mimeType) {
        this.input.setAttribute('accept', changes.mimeType.currentValue);
      } else {
        this.input.removeAttribute('accept');
      }
    }
  }

  fileSelected(e) {
    this.file = new Set<File>(e.target.files);
    let progress;
    if (this.page !== 'profile') {
      progress = this.uploadService.upload(this.file);
      this.progress.emit(progress);
    } else {
      this.subs.add(this.uploadService.uploadProfileImage(this.file).subscribe(res => {
        this.profileprogress.emit(res);
      }));
    }
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }
}
