/* tslint:disable:max-line-length */
import {Component, OnInit, AfterViewInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FileUploadStatusInterface} from '../../services/upload.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {debounce, debounceTime, tap} from 'rxjs/operators';
import {forkJoin, of} from 'rxjs';
import {EnumService} from '../../services/enum.service';
import {DropDown} from '../../models/classes/drop-down';
import {UserService} from '../../services/user.service';
import {ToastService} from '../../services/toast.service';
import {UserInterface} from '../../models/interfaces/user.interface';
import {StoryCreateInterface, StoryService} from '../../services/story.service';
import {NbToastrService} from '@nebular/theme';
import {User} from 'firebase';
import {SlateService} from '../../services/slate.service';
import * as _ from 'lodash';

declare var $: any;

@Component({
  selector: 'app-create-pitch',
  templateUrl: './create-pitch.component.html',
  styleUrls: ['./create-pitch.component.sass']
})
export class CreatePitchComponent implements OnInit, AfterViewInit {
  options = [
    {id: 1, label: 'One'},
    {id: 2, label: 'Two'},
    {id: 3, label: 'Three'}
  ];
  control = new FormControl();

  isEditing = false;
  isLoading = false;
  files: FileUploadStatusInterface[] = [];
  createSlateForm: FormGroup;
  formatDropdown: DropDown[];
  roleDropdown: DropDown[];
  userDetails: UserInterface;
  filteredOptions = [];
  submittedBy = new FormControl('', [Validators.required]);
  submittedByText = new FormControl();
  publishDate = new FormControl();
  selectedUser: UserInterface;
  storyId = '';
  isUserInvite = false;
  formatSelected: string;
  selectedSubmitedUser = {};
  isStory = false;
  roleType = '';
  isPublished = false;

  constructor(private route: ActivatedRoute, private router: Router, private toastr: NbToastrService, private toast: ToastService, private enumService: EnumService, private user: UserService, private story: StoryService, private slate: SlateService) {
    this.route.params.subscribe(res => {
      this.isEditing = !!res.id;
      this.storyId = res.id;
    });
    this.isStory = this.router.url.indexOf('story') !== -1 ? true : false;
    this.createSlateForm = new FormGroup({
      title: new FormControl('', [Validators.required]),
      small_desc: new FormControl('', [Validators.required]),
      budget: new FormControl('', [Validators.required]),
      estimate_time: new FormControl('', [Validators.required]),
      help_desc: new FormControl('', [Validators.required]),
      tags: new FormControl([]),
      submited_by: new FormControl(''),
      role_id: new FormControl('3', [Validators.required]),
      asset_ids: new FormControl([]),
      format: new FormControl('', [Validators.required]),
      byline: new FormControl('', [Validators.required]),
      is_user_invite: new FormControl('')
    });
    this.loadData();
    this.user.userSubject().subscribe(userData => {
      this.userDetails = userData;
    });
    this.createSlateForm.get('tags').valueChanges.subscribe(res => {
      if (!res.length) {
        $('tag-input-form input').attr('placeholder', 'To add a tag press [Enter]');
        $('tag-input-form input').css('width', '200px');
      }
    });
    this.roleType = this.user.getRoleType();
  }

  ngOnInit() {
    this.user.getUser();
  }

  ngAfterViewInit() {
    if (this.isEditing) {
      this.slate.getSlate(this.storyId).subscribe(res => {
        this.isPublished = res['is_publish'];
        let temp = res.format;
        this.formatSelected = temp.toLowerCase().replace(/\ /g, '_');
        this.submittedBy.setValue(res['submited_by'] ? res['submited_by'] : '');
        this.addFiles(res.asset.data);
        this.createSlateForm.patchValue(res);
        this.createSlateForm.get('format').setValue(this.formatSelected);
        // this.createSlateForm.get('assign_id').setValue(res.user.data.length ? res.user.data[0].id : (res['submitedby'].id ? res['submitedby'].id : ''));
        // this.createSlateForm.get('submitted_by').setValue(res.user.data.length ? res.user.data[0].name : (res['submitted_by'] ? res['submitted_by'] : ''));
        // this.createSlateForm.get('submittedby').setValue(res.user.data.length ? res.user.data[0].name : (res['submitedby'].name ? res['submitedby'].name : ''));
        if (this.isStory) {
          this.publishDate.setValue(new Date(res['publish_date']));
        }
      });
    }
    $('tag-input input').attr('placeholder', 'To add a tag press [Enter]').css('width', '200px');
  }

  search() {
    if (this.submittedBy.value.id) {
      this.user.getUsersByQuery(this.submittedBy.value.name).subscribe(users => {
        this.filteredOptions = users;
      });

    } else {
      this.user.getUsersByQuery(this.submittedBy.value).subscribe(users => {
        this.filteredOptions = users;
      });
    }
  }

  saveData() {
    if (this.createSlateForm.valid) {
      this.isLoading = true;
      this.createSlateForm.get('is_user_invite').setValue(this.isUserInvite);
      this.createSlateForm.get('role_id').setValue(parseInt(this.createSlateForm.value.role_id));
      this.createSlateForm.get('submited_by').setValue(this.submittedBy.value);
      if (this.createSlateForm.get('format').value.indexOf(' ') !== -1) {
        this.createSlateForm.get('format').setValue((this.createSlateForm.get('format').value).toLowerCase().replace(/\ /g, '_'));
      }
      if (this.isEditing) {
        let payload: StoryCreateInterface = this.createSlateForm.value;
        // let assign_id_arr = [];
        let role_id_arr = [];
        // assign_id_arr.push(this.createSlateForm.value.assign_id ? this.createSlateForm.value.assign_id : this.submittedBy.value);
        role_id_arr.push(this.createSlateForm.value.role_id);
        // this.createSlateForm.value.assign_id = assign_id_arr.length ? assign_id_arr : null;
        this.createSlateForm.value.role_id = role_id_arr;
        payload.story_id = this.storyId;
        if (this.isStory) {
          this.createSlateForm.value.publish_date = this.formatDate(this.publishDate.value);
        }
        this.story.updateStory(this.createSlateForm.value, this.storyId).subscribe(res => {
          if (res) {
            this.nextStepFunc();
          }
        }, err => {
          this.toast.showToast('', err.error.cause, 'danger', '');
        });
      } else {
        // this.createSlateForm.get('assign_id').setValue(this.createSlateForm.get('assign_id').value ? this.createSlateForm.get('assign_id').value : (this.submittedBy.value ? this.submittedBy.value : null));
        this.story.createStory(this.createSlateForm.value).subscribe(res => {
          if (res) {
            this.nextStepFunc();
          }
        }, err => {
          this.isLoading = false;
          this.toast.showToast('', err.error.cause, 'danger', '');
        });
      }
    }
  }

  publishStory() {
    this.isLoading = true;
    let payload = {
      is_publish: true,
      publish_date: this.formatDateAndTime(this.publishDate.value)
    };
    this.story.publishStory(payload, this.storyId).subscribe(res => {
      this.router.navigate(['dashboard']);
    });
  }

  unPublishStory() {
    this.isLoading = true;
    let payload = {
      is_publish: false,
      publish_date: null
    };
    this.story.publishStory(payload, this.storyId).subscribe(res => {
      this.router.navigate(['dashboard']);
    });
  }

  addFiles($e) {
    $e.forEach(file => {
      if (this.files.map(value => value.name === file.name).indexOf(true) === -1) {
        if (file.responseObs) {
          file.responseObs.pipe(tap(id => {
            const data = [...this.createSlateForm.get('asset_ids').value];
            data.push(id);
            this.createSlateForm.get('asset_ids').setValue(data);
          })).subscribe(id => file.id = id);
        } else {
          this.createSlateForm.get('asset_ids').value.push(file.id);
        }
        this.files.push(file);
      }
    });
    this.files = _.uniqBy(this.files, 'name');
  }

  remove($e) {
    this.files = this.files.filter(value => value.name !== $e.name);
    this.slate.remove({
      'asset_id': $e.id
    }, this.storyId).subscribe(res => {
    });
    let asset_ids;
    asset_ids = this.createSlateForm.get('asset_ids').value.filter(element => element !== $e.id);
    this.createSlateForm.get('asset_ids').setValue(asset_ids);
  }

  loadData() {
    forkJoin([this.enumService.getFormatList()
    ]).subscribe(res => {
      this.formatDropdown = res[0];
    });
  }

  formatDate(date: string) {
    let publishDate = new Date(date);
    return publishDate.getFullYear() + '-' + ((publishDate.getMonth() + 1) < 10 ? '0' + (publishDate.getMonth() + 1) : (publishDate.getMonth() + 1)) + '-' + ((publishDate.getDate()) < 10 ? '0' + (publishDate.getDate()) : (publishDate.getDate()));
  }

  formatDateAndTime(date: string) {
    let publishDate = new Date(date);
    let time = ' 00:00:00';
    return publishDate.getFullYear() + '-' + ((publishDate.getMonth() + 1) < 10 ? '0' + (publishDate.getMonth() + 1) : (publishDate.getMonth() + 1)) + '-' + ((publishDate.getDate()) < 10 ? '0' + (publishDate.getDate()) : (publishDate.getDate())) + time;
  }

  openNewMemberDialog(e) {
    this.isUserInvite = true;
    e.stopPropagation();
  }

  // inviteNewMember(storyId){
  //   this.user.inviteMember({
  //     email: this.submittedBy.value,
  //     role_id: parseInt(this.createSlateForm.get('role_id').value),
  //     story_id: storyId
  //   }).subscribe(res => {
  //     this.nextStepFunc();
  //   });
  // }

  nextStepFunc() {
    if (this.isStory && this.isEditing) {
      this.toast.showToast('', 'Story Edited', 'success', '');
      window.history.back();
    } else if (this.isEditing) {
      this.toast.showToast('', 'Pitch Edited', 'success', '');
      this.router.navigate(['slate']);
    } else {
      this.toast.showToast('', 'Pitch Created', 'success', '');
      this.router.navigate(['slate']);
    }
  }

  userHandle(value: UserInterface) {
    return value.id ? value.name : value;
  }
}
