/* tslint:disable:max-line-length */
import { Component, OnInit, AfterViewInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FileUploadStatusInterface } from '../../services/upload.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { debounce, debounceTime, tap } from 'rxjs/operators';
import { forkJoin, of } from 'rxjs';
import { EnumService } from '../../services/enum.service';
import { DropDown } from '../../models/classes/drop-down';
import { UserService } from '../../services/user.service';
import { CommentsService } from '../../services/comments.service';
import { UserInterface } from '../../models/interfaces/user.interface';
import { StoryInterface } from '../../models/interfaces/story.interface';
import { StoryCreateInterface, StoryService } from '../../services/story.service';
import { AuthService } from '../../services/auth.service';
import { NbToastrService } from '@nebular/theme';
import { User } from 'firebase';
import { SlateService } from '../../services/slate.service';
import { NbDialogRef } from '@nebular/theme';
declare var $: any;
@Component({
  selector: 'app-add-assignment-dialog',
  templateUrl: './add-assignment-dialog.component.html',
  styleUrls: ['./add-assignment-dialog.component.sass']
})

export class AddAssignmentDialogComponent implements OnInit {

  @Input() title: any;
  @Input() storyData: any;
  @Input() taskData: any;
  @Input() type: any = 'new';
  isEditing = false;
  files: FileUploadStatusInterface[] = [];
  assignmentData: FormGroup;
  userDetails: UserInterface;
  filteredOptions = [];
  submittedBy = new FormControl('');
  selectedUser: UserInterface;
  storyId = '';
  isUserInvite = false;
  formatSelected: string;
  submitedByValue = '';
  isLoading = true;
  storyMilestones: { id: number; status_title: string; }[];
  roleType = '';
  currentStoryData: any;
  deadlineDate: Date;
  // storyData: StoryInterface;

  constructor(private route: ActivatedRoute, private router: Router, private toastr: NbToastrService, private enumService: EnumService, private user: UserService, private story: StoryService, private slate: SlateService, protected dialogRef: NbDialogRef<{ title: any, storyData: any, taskData: any }>, public comments: CommentsService, public auth: AuthService) {
    this.comments.emptyCommentsArr();
    this.roleType = this.auth.getRoleType();
    this.assignmentData = new FormGroup({
      title: new FormControl({ value: '', disabled: this.roleType === 'author' || this.roleType === 'contributor' }, [Validators.required]),
      description: new FormControl({ value: '', disabled: this.roleType === 'author' || this.roleType === 'contributor' }, [Validators.required]),
      deadline: new FormControl({ value: '', disabled: this.roleType === 'author' || this.roleType === 'contributor' }, [Validators.required]),
      assign_id: new FormControl('', [Validators.required]),
      role_id: new FormControl('4', [Validators.required]),
      asset_ids: new FormControl([]),
      task_status_id: new FormControl('1', [Validators.required]),
      story_milestone_id: new FormControl({ value: '', disabled: this.roleType === 'author' || this.roleType === 'contributor' }, [Validators.required]),
    });
    if (this.roleType === 'author' || this.roleType === 'contributor') {
      this.submittedBy = new FormControl({ value: '', disabled: this.roleType === 'author' || this.roleType === 'contributor' });
      $('.assignedTo').attr('disabled', true);
    }
    this.user.userSubject().subscribe(userData => {
      this.userDetails = userData;
    });
    this.submittedBy.valueChanges.pipe(debounceTime(800)).subscribe(res => {
      this.assignmentData.get('assign_id').setValue(typeof this.submittedBy.value === 'object' ? this.submittedBy.value.id : this.submittedBy.value);
      this.user.getUsersByQuery(res).subscribe(user => {
        this.filteredOptions = user;
      });
    });
  }

  ngOnInit() {
    this.user.getUser();
    this.assignmentData.get('title').setValue(this.title ? this.title : '');
    this.story.getTaskStatusList().subscribe(res => {
      this.storyMilestones = res;
      if (this.taskData) {
        this.assignmentData.get('title').setValue(this.taskData.title ? this.taskData.title : '');
        this.assignmentData.get('description').setValue(this.taskData.description ? this.taskData.description : '');
        this.assignmentData.get('deadline').setValue(this.taskData.deadline ? new Date(this.taskData.deadline) : '');
        this.taskData.deadline ? this.deadlineDate = new Date(this.taskData.deadline) : '';
        if (this.taskData.task_status) {
          this.storyMilestones.forEach(milestone => {
            if (milestone.status_title === this.taskData.task_status) {
              this.assignmentData.get('task_status_id').setValue(milestone.id);
            }
          })
        }
        this.submitedByValue = this.taskData.name;
        this.user.getUsersByQuery('').subscribe(users => {
          if (this.taskData.user_id) {
            this.filteredOptions = users.filter(ele => ele.id === this.taskData.user_id)
            this.submittedBy.setValue(this.filteredOptions[0]);
          }
        })
        this.assignmentData.get('assign_id').setValue(this.taskData.user_id ? this.taskData.user_id : '');
        if (this.taskData.asset) {
          this.addFiles(this.taskData.asset.data);
        }

        if (this.taskData.milestone_status && this.storyData) {
          this.storyData.milestone.data.forEach(milestone => {
            if (milestone.story_milestone_title === this.taskData.milestone_status) {
              this.assignmentData.get('story_milestone_id').setValue(milestone.story_milestone_id);
            }
          })
        } else if (this.taskData.milestone_status && !this.storyData) {
          this.story.getCurrentStoryMilestones(this.taskData.story_id).subscribe(res => {
            this.currentStoryData = res['story_milestone'];
            this.currentStoryData.forEach(milestone => {
              if (milestone.story_milestone_title === this.taskData.milestone_status) {
                this.assignmentData.get('story_milestone_id').setValue(milestone.story_milestone_id);
              }
            })
          })
        }
        if (this.type === 'old') {
          this.story.setStoryId(this.taskData.id);
        }
      }
    });
    if (this.roleType === 'author' || this.roleType === 'contributor') {
      $('.assignedTo').attr('disabled', true);
    }
  }

  isCommentVisible() {
    if (this.storyData || this.taskData.story_id) return true;
    return false;
  }

  saveData() {
    if (this.assignmentData.valid) {
      this.assignmentData.value.story_id = this.storyData && this.storyData.id ? this.storyData.id : this.taskData.story_id;
      this.assignmentData.value.is_user_invite = this.isUserInvite;
      this.assignmentData.value.role_id = parseInt(this.assignmentData.value.role_id);
      this.assignmentData.value.assign_id = this.assignmentData.value.assign_id ? this.assignmentData.value.assign_id : this.submittedBy.value;
      console.log(this.assignmentData.value.deadline, this.deadlineDate);
      this.assignmentData.value.deadline = this.formatDate(this.assignmentData.value.deadline ? this.assignmentData.value.deadline : this.deadlineDate);
      console.log(this.assignmentData.value.deadline);
      if (this.taskData) {
        this.assignmentData.value.task_id = this.taskData.id;
        this.story.updateTask(this.assignmentData.value, this.storyData ? this.storyData.id : this.taskData.story_id).subscribe(res => {
          if (res) {
            this.toastr.success('', 'Assignment Updated', { icon: '' });
            this.closeDialog();
          }
        });
      }
      else {
        this.story.createTask(this.assignmentData.value, this.storyData.id).subscribe(res => {
          if (res) {
            this.toastr.success('', 'Assignment Created', { icon: '' });
            this.closeDialog();
          }
        });
      }
    }
  }

  formatDate(date: string) {
    let publishDate = new Date(date);
    return publishDate.getFullYear() + '-' + ((publishDate.getMonth() + 1) < 10 ? '0' + (publishDate.getMonth() + 1) : (publishDate.getMonth() + 1)) + '-' + ((publishDate.getDate()) < 10 ? '0' + (publishDate.getDate()) : (publishDate.getDate()));
  }

  addFiles($e) {
    $e.forEach(file => {
      if (this.files.map(value => value.name === file.name).indexOf(true) === -1) {
        if (file.responseObs) {
          file.responseObs.pipe(tap(id => {
            const data = [...this.assignmentData.get('asset_ids').value];
            data.push(id);
            this.assignmentData.get('asset_ids').setValue(data);
          })).subscribe(id => file.id = id);
        }
        else {
          this.assignmentData.get('asset_ids').value.push(file.id);
        }
      }
      this.files.push(file);
    });
  }

  remove($e) {
    this.files = this.files.filter(value => value.name !== $e.name);
    let asset_ids;
    asset_ids = this.assignmentData.get('asset_ids').value.filter(element => element !== $e.id);
    this.assignmentData.get('asset_ids').setValue(asset_ids);
  }

  markComplete() {
    this.assignmentData.get('task_status_id').setValue(4);
  }

  onSelectionChange(event) {
    this.isUserInvite = false;
    if (event) {
      this.assignmentData.get('assign_id').setValue(event.id);
    }
    this.selectedUser = event;
  }

  openNewMemberDialog(e) {
    this.isUserInvite = true;
    e.stopPropagation();
  }

  userHandle(value: UserInterface) {
    return value.id ? value.name : value;
  }

  search() {
    if (this.submittedBy && this.submittedBy.value) {
      if (this.submittedBy.value.id) {
        console.log(this.submittedBy.value.name)
        this.user.getUsersByQuery(this.submittedBy.value.name).subscribe(users => {
          this.filteredOptions = users;
        });

      } else {
        console.log(this.submittedBy.value)
        this.user.getUsersByQuery(this.submittedBy.value).subscribe(users => {
          this.filteredOptions = users;
        });
      }
    }
  }
  checkPublishDateStatus(item): string {
    var publishDate = (Date.parse(item));
    var todaysDate = (Date.now());
    return (publishDate < todaysDate) ? 'not on' : 'on';
  }

  closeDialog() {
    this.dialogRef.close();
  }

  formatMilestone(milestone) {
    return milestone ? ((milestone.split('_')).filter(val => val[0].toUpperCase())).join(' ') : ''
  }

}
