import {Component, OnDestroy, OnInit} from '@angular/core';
import {DashboardService} from '../../services/dashboard.service';
import {StoryInterface} from '../../models/interfaces/story.interface';
import {StoryGroupInterface} from '../../models/interfaces/story-group.interface';
import {TaskInterface} from '../../models/interfaces/task.interface';
import {UserService} from '../../services/user.service';
import {ToastService} from '../../services/toast.service';
import {AuthService} from '../../services/auth.service';
import {SubSink} from 'subsink';
import {Router} from '@angular/router';
import {NbDialogService} from '@nebular/theme';
import {AddAssignmentDialogComponent} from 'src/app/elements/add-assignment-dialog/add-assignment-dialog.component';
import {NbToastrService} from '@nebular/theme';

@Component({
  selector: 'app-dashboard-page',
  templateUrl: './dashboard-page.component.html',
  styleUrls: ['./dashboard-page.component.sass']
})
export class DashboardPageComponent implements OnInit, OnDestroy {

  tempStoryList: StoryInterface[] = [];
  storyList: StoryGroupInterface[] = [];
  loading = true;
  gettingStories = true;
  type = '';
  taskList: TaskInterface[] = [];
  // myAssignments: TaskInterface[] = [];
  allTasks: [
    { name: 'Overdue', data: TaskInterface[] },
    { name: 'Today', data: TaskInterface[] },
    { name: 'Coming Up', data: TaskInterface[] },
    { name: 'No Deadline', data: TaskInterface[] }] =
    [{name: 'Overdue', data: []},
      {name: 'Today', data: []},
      {name: 'Coming Up', data: []},
      {name: 'No Deadline', data: []}];
  myAssignments: [
    { name: 'Overdue', data: TaskInterface[] },
    { name: 'Today', data: TaskInterface[] },
    { name: 'Coming Up', data: TaskInterface[] },
    { name: 'No Deadline', data: TaskInterface[] }] =
    [{name: 'Overdue', data: []},
      {name: 'Today', data: []},
      {name: 'Coming Up', data: []},
      {name: 'No Deadline', data: []}];
  tempDateArr = [];
  roleType = '';
  subs = new SubSink();
  allAssignmentsLength = 0;
  userId;
  myAssignmentsLength = 0;
  getAssignmentList = true;

  constructor(public dashboard: DashboardService, public user: UserService, public router: Router, public auth: AuthService, public toastr: NbToastrService, public dialogService: NbDialogService, public toast: ToastService) {
    this.loading = true;
  }

  ngOnInit() {
    this.roleType = this.auth.getRoleType();
    this.type = this.user.getCurrentUserRole() && this.user.getCurrentUserRole().role ? this.user.getCurrentUserRole().role : '';
    this.userId = this.user.getCurrentUserRole() && this.user.getCurrentUserRole().id ? this.user.getCurrentUserRole().id : '';
    if (this.type) {
      this.getDashboardData(false);
    } else {
      this.loading = false;
    }
  }

  getDashboardData(isAssignment) {
    this.loading = true;
    this.subs.sink = this.dashboard.getStoryList(this.userId).subscribe(res => {
      this.tempStoryList = this.type === 'editor' ? res.story : (this.type === 'chief_editor' ? res.chief_editor : res.story);
      this.taskList = this.type === 'editor' ? (res.user_task ? res.user_task.data : []) : [];
      if (this.tempStoryList.length) {
        this.findStoryList(isAssignment);
      }
      if (this.taskList.length && this.type !== 'contributor') {
        this.findTaskList();
      }
      this.loading = false;
    }, err => {
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  findStoryList(isAssignment) {
    this.tempDateArr = [];
    this.storyList = [];
    this.tempStoryList.forEach(val => {
      let dateFetched = new Date(val.created_at).toDateString();
      let datePresent = false;
      let _this = this;
      this.tempDateArr.forEach(date => {
        if (dateFetched === date) {
          datePresent = true;
        }
      });
      if (!datePresent) {
        this.storyList.push({created_at: dateFetched, story: []});
        this.storyList[(this.storyList.length) - 1].story.push(val);
        this.tempDateArr.push(dateFetched);
      } else {
        this.storyList.forEach(function(element, index) {
          if (element.created_at === dateFetched) {
            _this.storyList[index].story.push(val);
          }
        });
      }
    });
    if (this.type === 'contributor' && !isAssignment) {
      this.storyList && this.storyList.length && this.storyList[0].story && this.storyList[0].story.length ? this.storyClicked(this.storyList[0].story[0]) : '';
    } else if (this.type === 'contributor' && isAssignment) {

    }
  }

  findTaskList() {
    if (this.allTasks) {
      this.emptyAllTasks();
    }

    if (this.myAssignments) {
      this.emptyMyAssignments();
    }

    this.subs.sink = this.user.userSubject().subscribe(res => {
      if (this.getAssignmentList) {
        this.taskList.forEach(value => {
          if (value.deadline && (value.task_status !== 'completed' && value.task_status !== 'Completed')) {
            let currentDate = Date.parse(new Date().toDateString());
            let taskDate = Date.parse(new Date(value.deadline.split(' ')[0]).toDateString());
            if (currentDate === taskDate) {
              this.allTasks[1].data.push(value);
              if (value.user_id === res.id) {
                this.myAssignments[1].data.push(value);
              }
            } else if (currentDate > taskDate) {
              this.allTasks[0].data.push(value);
              if (value.user_id === res.id) {
                this.myAssignments[0].data.push(value);
              }
            } else {
              this.allTasks[2].data.push(value);
              if (value.user_id === res.id) {
                this.myAssignments[2].data.push(value);
              }
            }
          } else if (!value.deadline && (value.task_status !== 'completed' && value.task_status !== 'Completed')) {
            this.allTasks[3].data.push(value);
            if (value.user_id === res.id) {
              this.myAssignments[3].data.push(value);
            }
          }
        });
        this.allAssignmentsLength = this.allTasks[0].data.length + this.allTasks[1].data.length + this.allTasks[2].data.length + this.allTasks[3].data.length;
        this.myAssignmentsLength = this.myAssignments[0].data.length + this.myAssignments[1].data.length + this.myAssignments[2].data.length + this.myAssignments[3].data.length;
      }
    });
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  assignmentClicked(data) {
    this.getAssignmentList = false;
    this.loading = true;
    if (name && name === 'Completed' && this.roleType !== 'editor') {
      return;
    }
    if (this.roleType === 'ats_core') {
      return;
    }
    this.dialogService.open(AddAssignmentDialogComponent,
      {
        context: {title: data.title, taskData: data, type: 'old'}
      })
      .onClose.subscribe(() => {
      this.getAssignmentList = true;
      // this.emptyMyAssignments();
      // this.emptyAllTasks();
      this.getDashboardData(true);
      this.loading = false;
      this.type === 'contributor' ? this.storyClicked(data) : '';
    });
  }

  emptyMyAssignments() {
    this.myAssignmentsLength = 0;
    this.myAssignments.forEach(type => {
      type['data'] = [];
    });
  }

  emptyAllTasks() {
    this.allAssignmentsLength = 0;
    this.allTasks.forEach(type => {
      type['data'] = [];
    });
  }

  storyClicked(data) {
    this.gettingStories = true;
    if (this.type === 'contributor') {
      this.taskList = [];
      if (this.myAssignments) {
        this.emptyMyAssignments();
        this.gettingStories = false;
      }
      this.dashboard.getTaskList(data.story_id, this.userId).subscribe(res => {
        this.taskList = res;
        this.findTaskList();
        this.gettingStories = false;
      }, err => {
        this.toast.showToast('', err.error.cause, 'danger', '');
      });
    } else {
      this.router.navigateByUrl(`story/${data.id}`);
    }
  }
}
