import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ActiveTabService {

  public slateUrl = ['slate', 'edit-pitch', 'create-pitch'];
  public dashboardUrl = ['dashboard', 'story'];
  public storiesUrl = ['published-stories'];

  constructor() {
  }

  findActiveTab(url) {
    return (this.slateUrl.filter(value => url.includes(value))).length ? 1 : (this.dashboardUrl.filter(value => url.includes(value)).length ? 0 : this.storiesUrl.filter(value => url.includes(value)).length ? 2 : null);
  }

}
