import {Component, OnInit} from '@angular/core';
import {SlateService} from '../../services/slate.service';
import {CeSlateInterface} from '../../models/interfaces/ce-slate.interface';

@Component({
  selector: 'app-slate-list',
  templateUrl: './slate-list.component.html',
  styleUrls: ['./slate-list.component.sass']
})
export class SlateListComponent implements OnInit {
  slateList: CeSlateInterface[] = [];
  loading = true;
  slateCount = 0;
  approvedCount = 0;

  constructor(private slate: SlateService) {
    this.slate.getCeSlateList().subscribe(res => {
      this.slateList = res;
      this.slateList.forEach(element=>{
        if(element.story_status === 'approved'){
          this.approvedCount ++;
        }else{
          this.slateCount ++;
        }
      })
      this.slateList = this.slateList.reverse();
      this.loading = false;
    });
  }

  ngOnInit() {
  }

}
