import { Injectable } from '@angular/core';
import { HttpResponseInterface } from '../models/interfaces/http-response.interface';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Constants } from '../utils/utils';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(public http: HttpClient) { }

  getStoryList(id) {
    let headers = new HttpHeaders({
      'role': id.toString(),
      'Content-Type': 'application/x-www-form-urlencoded'
    });
    console.log("getStoryList", id);
    return this.http.get<HttpResponseInterface>(Constants.host + 'dashboard', { headers: headers }).pipe(map(res => res.data));
  }

  getTaskList(story_id, user_id) {
    let taskHeaders = new HttpHeaders({
      'story': story_id.toString(),
      'role': user_id.toString()
    });
    console.log("getTaskList", story_id, user_id);
    return this.http.get<HttpResponseInterface>(Constants.host + 'storytask', { headers: taskHeaders }).pipe(map(res => res.data.data));
  }
}
