/* tslint:disable:max-line-length */
import {Component, OnInit} from '@angular/core';
import {Location} from '@angular/common';
import {AuthService} from '../../services/auth.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';
// @ts-ignore
import {CustomValidators} from '@parth181195/ngx-validators';
import {AuthStatusEnum} from '../../models/enums/auth-status.enum';
import {ActivatedRoute, NavigationEnd, Router} from '@angular/router';
import {NbToastrService} from '@nebular/theme';
import {SignUpInputInterface} from '../../models/interfaces/signup.interface';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.sass']
})
export class LoginPageComponent implements OnInit {
  currentStep: 1 | 2 | 3 | 4 | 'loading' = 1;
  emailForm: FormGroup;
  otpForm: FormGroup;
  nameForm: FormGroup;
  stepRegister: { current: 1 | 2 | 3 | 4 | 'loading', previous?: 1 | 2 | 3 | 4 | 'loading', loading?: boolean } = {
    current: 1,
    loading: false
  };

  errMsg = '';

  constructor(private auth: AuthService, private router: Router, private toastrService: NbToastrService, private route: ActivatedRoute, private location: Location) {
    this.route.queryParams.subscribe(res => {
      // if (res.step) {
      //   this.currentStep = res.step;
      //   this.stepRegister.current = res.step;
      // }
    });
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd && event.urlAfterRedirects.includes('login') && this.auth.getAuthState().getValue().status === AuthStatusEnum.loggedIn) {
        this.router.navigate(['dashboard']);
      }
    });
    this.emailForm = new FormGroup({
      // @ts-ignore
      email: new FormControl('', [Validators.required])
      // email: new FormControl('', [Validators.required, CustomValidators.email])
    });
    this.nameForm = new FormGroup({
      // @ts-ignore
      name: new FormControl('', [Validators.required, CustomValidators.alphabet])
    });
    this.otpForm = new FormGroup({
      // @ts-ignore
      otp: new FormControl('', [Validators.required, CustomValidators.hasLength(4)])
    });
  }

  ngOnInit() {
  }

  loginWith(method: string) {
    if (method === 'google') {
      this.auth.loginWithGoogle().subscribe(res => {
        if (res.status === AuthStatusEnum.notDetermined) {
          this.backToStep('loading');
        } else if (res.status === AuthStatusEnum.loggedIn) {
          this.router.navigate(['dashboard']);
        } else {
          this.toastrService.danger('', 'Login Failed', {
            icon: ''
          });
          this.backToStep(1);
        }
      }, error => {
        this.toastrService.danger(error.error.cause);
        this.errMsg = error.error.cause;
      });
    } else {
      this.backToStep(2);
    }
  }


  sendOtp() {
    this.otpForm.reset();
    if (this.emailForm.get('email').valid) {
      this.backToStep('loading');
      this.auth.getOtp(this.emailForm.get('email').value).subscribe(res => {
        this.backToStep(3);
      }, (err) => {
        this.errMsg = err.error.cause;
        this.backToStep(2);
      });
    }
  }


  onOtpChange(event: string) {
    this.otpForm.get('otp').setValue(event);

  }

  backToStep(step: 1 | 2 | 3 | 4 | 'loading', resetForm = false) {
    if (step !== 'loading') {
      this.location.go('/login', `step=${step.toString()}`);
    }
    if (this.stepRegister.current !== 'loading') {
      this.stepRegister.current = step;
      this.stepRegister.previous = this.currentStep;
    } else {
      this.stepRegister.current = step;
      this.stepRegister.loading = true;
      // this.stepRegister.previous = this.currentStep;
    }
    this.currentStep = step;
    if (resetForm) {
      this.errMsg = '';
      this.otpForm.reset();
      this.emailForm.reset();
    }
  }

  confirmOtp() {
    const value = this.otpForm.get('otp').value;
    if (value.length === 4) {
      this.backToStep('loading');
      this.auth.checkOtp(this.emailForm.get('email').value, parseInt(value, 0)).subscribe(res => {
        if (res.data.is_registered) {
          this.router.navigate(['dashboard']);
        } else {
          this.backToStep(4);
        }
        // this.backToStep4);
      }, err => {
        this.errMsg = err.error.cause;
        this.backToStep(3);
      });
      //   this.auth.getOtp(this.emailForm.get('email').value);
      //   this.currentStep = 3;
    }
  }

  addDetails() {
    const payload: SignUpInputInterface = {
      email: this.emailForm.value.email,
      name: this.nameForm.value.name
    };
    this.auth.signUp(payload).subscribe(res => {
      if (res.data.token) {
        this.router.navigate(['dashboard']);
      }
    });
  }
}


