import {Component, OnInit} from '@angular/core';
import {CeSlateInterface} from '../../models/interfaces/ce-slate.interface';
import {ActivatedRoute} from '@angular/router';
import {SlateService} from '../../services/slate.service';
import {StoryService} from '../../services/story.service';
import {NbToastrService} from '@nebular/theme';
import {UserDialogService} from '../../elements/user-dialog/user-dialog.service';
import {CeSlateCardComponent} from '../../elements/ce-slate-card/ce-slate-card.component';
import {UserSelectComponent} from '../../elements/user-select/user-select.component';

@Component({
  selector: 'app-ce-approve-slate',
  templateUrl: './ce-approve-slate.component.html',
  styleUrls: ['./ce-approve-slate.component.sass']
})
export class CeApproveSlateComponent implements OnInit {

  isLoading = true;
  slateData: CeSlateInterface;
  slateId: number;

  constructor(private route: ActivatedRoute, private  slate: SlateService, private story: StoryService, private toastr: NbToastrService, private dialog: UserDialogService) {
    this.route.params.subscribe(res => {
      this.slateId = res.id;
      // this.open();
      this.slate.getSlate(this.slateId).subscribe(res => {
        this.isLoading = false;
        this.slateData = res;
      });
    });
  }


  ngOnInit() {
  }

  open(e?) {
    let ref = this.dialog.open(UserSelectComponent, {data: {evt: e, multi: true}});
    ref.afterClosed.subscribe(res => {
    });
  }
}
