import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Constants} from '../utils/utils';
import {map, tap} from 'rxjs/operators';
import {HttpResponseInterface} from '../models/interfaces/http-response.interface';
import {UserInterface} from '../models/interfaces/user.interface';
import {StoryService} from '../services/story.service';
import {BehaviorSubject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CommentsService {

  comments: Array<CommentsInterface[]> = [];
  commentsMeta = [];
  commentSub = new BehaviorSubject<Partial<CommentsInterface>[]>([undefined]);

  constructor(private http: HttpClient, private story: StoryService) {
  }

  getUserRole() {
    let role = JSON.parse(sessionStorage.getItem('user_role'));
    return role && role['id'] ? role['id'].toString() : '';
  }

  createComment(payload: CreateCommentInterFace, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface>(Constants.host + 'createcomment', payload, {headers: headers}).pipe(tap(res => {
      if (payload.parent_comment_id) {
        let comment_list = this.comments[this.comments.length - 1];
        for (let i = 0; i < comment_list.length; i++) {
          if (comment_list[i].id == payload.parent_comment_id) {
            this.comments[this.comments.length - 1][i].child.data.push(res.data);
            this.commentSub.next(this.sortByDate(this.comments[this.comments.length - 1], 'created_at'));
            break;
          }
        }
      } else {
        this.comments[this.comments.length - 1].push(res.data);
        this.commentSub.next(this.sortByDate(this.comments[this.comments.length - 1], 'created_at'));
      }
    }));
  }

  sortByDate(data, key) {
    return data.sort(function(a, b) {
      var x = Math.round(new Date(a[key]).getTime() / 1000);
      var y = Math.round(new Date(b[key]).getTime() / 1000);
      return ((x > y) ? -1 : ((x < y) ? 1 : 0));
    });
  }

  getComments(payload: {
    commentable_id: string,
    commentable_type: string
  }, storyId) {
    this.comments = [];
    this.commentsMeta = [];
    let headers;
    headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    this.http.post<HttpResponseInterface<{ comments: CommentsInterface[], meta: {} }>>(Constants.host + 'commenthistory', payload, {headers: headers})
      .pipe(tap(res => {
        this.comments = [];
        this.comments.push(res.data.comments);
        this.commentsMeta.push(res.data.meta);
      }), map(res => res.data.comments)).subscribe(data => {
      this.commentSub.next(this.sortByDate(data, 'created_at'));
    });
  }

  getNextCommentsPage(payload: {
    commentable_id: string,
    commentable_type: string
  }, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post(Constants.host + 'commenthistory?page', payload, {headers: headers}).pipe(tap(res => {
    }), map(res => {
    }));
  }

  emptyCommentsArr() {
    this.commentSub.next([]);
  }
}


export interface CreateCommentInterFace {
  user_id: number
  commentable_id: number
  comment_text: string
  commentable_type: string
  parent_comment_id?: number
  asset_ids?: number[]
}

export interface CommentsInterface {
  asset: { data: [] }
  child: { data: CommentsInterface[] }
  mentions: { data: UserInterface[] }
  comment_text: string
  commentable_id: number
  commentable_type: string
  created_at: string
  id: number
  parent_comment_id: null
  user: Partial<UserInterface>;
}
