import {
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  OnInit,
  Output,
  ViewChild,
  AfterViewInit,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import {FormControl, FormGroup} from '@angular/forms';
import {UserInterface} from '../../../models/interfaces/user.interface';
import {UserService} from '../../../services/user.service';
import {ActivatedRoute, Router} from '@angular/router';
import {CommentsService, CreateCommentInterFace} from '../../../services/comments.service';
import {FileUploadStatusInterface} from '../../../services/upload.service';
import {tap} from 'rxjs/operators';

@Component({
  selector: 'app-comment-input',
  templateUrl: './comment-input.component.html',
  styleUrls: ['./comment-input.component.sass']
})
export class CommentInputComponent implements OnInit, AfterViewInit, OnChanges {
  commentForm: FormGroup;
  isFocused = false;
  isLoading = false;
  inputComment = false;
  isTaggingUser = false;
  user: Partial<UserInterface>;
  @Input() isReplying;
  @Input() type = 'story';
  @Input() id;
  @Input() story_id;
  @Output() commentAdded = new EventEmitter<any>();
  @ViewChild('commentInput') commentElement: ElementRef;
  isReplyingBoolean = false;
  files: FileUploadStatusInterface[] = [];
  commentInputHistory = '';
  storyId: string;
  mentionedUsers = [];
  config = {triggerChar: '@', maxItems: 10, labelKey: 'name'};
  items = [];

  constructor(private userService: UserService, private route: ActivatedRoute, private router: Router, private commentsService: CommentsService) {
    this.route.params.subscribe(params => {
      params.id ? this.storyId = params.id : (this.router.url.split('story/')[1] ? this.storyId = this.router.url.split('story/')[1] : this.storyId = this.story_id);
    });
    this.commentForm = new FormGroup({
      inputControl: new FormControl(''),
      asset_ids: new FormControl('')
    });
    // this.commentForm.get('inputControl').valueChanges.subscribe(value => {
    //   console.log(value);
    // });
  }

  emptyFn() {
  }

  onUserSelect(e) {
    console.log(e);
    this.mentionedUsers.push(e);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.isReplying) {
      this.isReplying = changes.isReplying.currentValue;
      this.isReplyingBoolean = true;
      this.isFocused = true;
    }
  }

  ngOnInit() {
    if (!this.storyId && this.story_id) {
      this.storyId = this.story_id;
    }
    this.userService.getUser();
    this.userService.userSubjectSource.subscribe(res => {
      this.user = res;
    });
  }

  userSearch(e) {
    if (this.type === 'story') {
      this.userService.getTeamByQuery(e, parseInt(this.storyId, 0)).subscribe(users => {
        this.items = users.map(user => {
          return {name: user.name, id: user.id};
        });
        console.log(this.user);
      });
    } else {
      this.userService.getUsersByQuery(e,).subscribe(users => {
        this.items = users.map(user => {
          return {name: user.name, id: user.id};
        });
        console.log(this.user);
      });
    }
  }

  ngAfterViewInit() {
    if (this.isReplying) {
      this.isReplyingBoolean = true;
      this.isFocused = true;
      this.commentElement.nativeElement.focus();
    }
  }

  addFiles($e: FileUploadStatusInterface[]) {
    $e.forEach(file => {
      if (this.files.map(value => value.name === file.name).indexOf(true) === -1) {
        file.responseObs.pipe(tap(id => {
          const data = [...this.commentForm.get('asset_ids').value];
          data.push(id);
          this.commentForm.get('asset_ids').setValue(data);
        })).subscribe(id => file.id = id);
        this.files.push(file);
      }
    });
  }

  remove($e: FileUploadStatusInterface) {
    this.commentForm.get('asset_ids').setValue([]);
    let data = [];
    this.files = this.files.filter(value => value.name !== $e.name);
    this.files.forEach(res => data.push(res.id));
    this.commentForm.get('asset_ids').setValue(data);
  }

  // getStringArray() {
  //   return this.items.map(entry => entry.name);
  // }

  saveData() {
  }

  focused(e, shouldFocus = false) {
    if (shouldFocus) {
      this.commentElement.nativeElement.focus();
    }
    this.isFocused = true;
    if (this.isReplying) {
      this.isReplyingBoolean = true;
    }
  }

  outOfFocus(e,) {
    this.isFocused = false;
    this.isReplyingBoolean = false;
    this.commentElement.nativeElement.blur();
  }

  taggerOpened() {
    this.isTaggingUser = true;
    console.log(this.isTaggingUser);
  }

  taggerClosed() {
    console.log(this.commentForm.get('inputControl').value);
    setTimeout(() => {
    this.isTaggingUser = false;
    },300);
  }

  createComment(e?) {
    if (this.isTaggingUser) {
      return;
    }
    if (this.commentForm.get('inputControl').value !== '' || this.files.length !== 0) {
      this.isLoading = true;
      let text = this.commentForm.get('inputControl').value;
      this.mentionedUsers.forEach(user => {
          console.log(user);
          console.log(text.includes(`@${user.item.name}`));
          if (text.includes(`@${user.item.name}`)) {
            text = text.replace(`@${user.item.name}`, `<<${user.item.id}>>`);

          }
        }
      );
      const payload: CreateCommentInterFace = {
        comment_text: text,
        user_id: this.user.id,
        commentable_type: this.type,
        commentable_id: parseInt(this.id ? this.id : this.storyId, 0),
        asset_ids: this.commentForm.get('asset_ids').value
      };
      if (this.isReplying) {
        payload.parent_comment_id = this.isReplying;
      }
      this.commentsService.createComment(payload, this.storyId).subscribe(res => {
        this.files = [];
        this.isLoading = false;
        this.isFocused = false;
        this.commentAdded.emit(true);
        setTimeout(time => {
          this.commentForm.get('asset_ids').setValue('');
          this.commentForm.get('inputControl').setValue('');
        }, 50);
      }, err => {
        this.commentAdded.emit(true);
        this.isLoading = false;
        this.isFocused = false;
      });
    }
  }
}
