import { Component, OnInit, Input, OnChanges, SimpleChange, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-published-story-card',
  templateUrl: './published-story-card.component.html',
  styleUrls: ['./published-story-card.component.sass']
})
export class PublishedStoryCardComponent implements OnInit, OnChanges {
  @Input() data: any = {};
  tags = false;

  constructor() { }

  ngOnInit() {
  }
  ngOnChanges(simpleChanges: SimpleChanges){
    if(simpleChanges.data){
      if(simpleChanges.data.currentValue.tags){
        this.tags = true;

      }
    }
  }

  // tagsExist() {
  // }

  chooseFormatIcon() {
    switch (this.data.format) {
      case 'Video Essay':
        return 'activity-outline';
      case 'Photo Essay':
        return 'image-outline';
      default:
        return 'file-outline';
    }
  }

}
