import { Component, OnInit, Input, AfterViewInit} from '@angular/core';
import { AssetInterface } from 'src/app/models/interfaces/asset.interface';
declare var require: any
declare var $: any
@Component({
  selector: 'app-asset-view-chip',
  templateUrl: './asset-view-chip.component.html',
  styleUrls: ['./asset-view-chip.component.sass']
})
export class AssetViewChipComponent implements OnInit, AfterViewInit {

  @Input() data: AssetInterface[];
  @Input() type = 'story';
  mime = require('mime');
  entered = false;
  assetGroupNumber;
  constructor() {
  }

  ngOnInit() {
  }

  ngAfterViewInit(){
    this.assetGroupNumber = this.data && this.data.length ? this.data[0].id : 'asset-list';
    $('#'+this.assetGroupNumber+' .asset-wrapper').attr('data-lightbox',this.assetGroupNumber);
    if(this.type === 'story'){
      $('div#lightbox').removeClass('task');
    }else{
      $('div#lightbox').addClass('task');
    }
  }

  checkMimetype(asset): boolean {
    return this.mime.getType(asset.asset_url).includes('image');
  }

  downloadFile(url) {
    window.location.href = url;
  }
}
