import { Injectable } from '@angular/core';
import { HttpResponseInterface } from '../models/interfaces/http-response.interface';
import { HttpClient } from '@angular/common/http';
import { Constants } from '../utils/utils';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class PublishedStoriesService {

  constructor(public http: HttpClient) { }

  getPublishedStory() {
    return this.http.get<HttpResponseInterface>(Constants.host + 'publishstorylist').pipe(map(res => res.data['publish_stories']));
  }
}
