/* tslint:disable:max-line-length */
import {Injectable} from '@angular/core';
import {BehaviorSubject, Observable, Subject} from 'rxjs';
import {UserInterface} from '../models/interfaces/user.interface';
import {HttpClient} from '@angular/common/http';
import {Constants} from '../utils/utils';
import {tap, map} from 'rxjs/operators';
import {HttpResponseInterface} from '../models/interfaces/http-response.interface';
import {User} from 'firebase';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  static UserDataKey = 'user';
  static UserRole = 'user_role';
  protected userHttp = this.http.get<HttpResponseInterface<UserInterface>>(Constants.host + 'userdetails');
  userSubjectSource = new BehaviorSubject<UserInterface | any>({});

  constructor(private http: HttpClient) {
  }

  userSubject() {
    return this.userSubjectSource.asObservable();
  }

  getUserData() {
    return JSON.parse(sessionStorage.getItem(UserService.UserDataKey));
  }

  getUser() {
    if (sessionStorage.getItem(UserService.UserDataKey)) {
      let data: UserInterface = JSON.parse(sessionStorage.getItem(UserService.UserDataKey));
      this.userSubjectSource.next(data);
      // this.userHttp.subscribe();
    } else {
      this.userHttp.subscribe(res => {
        if (!res.error) {
          sessionStorage.setItem(UserService.UserDataKey, JSON.stringify(res.data));
          this.userSubjectSource.next(res.data);
        }
      });
    }

  }

  protected updateUserEverywhere() {
    this.userHttp.subscribe(res2 => {
      if (!res2.error) {
        sessionStorage.setItem(UserService.UserDataKey, JSON.stringify(res2.data));
        this.userSubjectSource.next(res2.data);
      }
    });
  }


  inviteUser(payload) {
    return this.http.post(Constants.host + 'inviteuser', payload);
  }

  inviteMember(payload: { email: string, role_id: number, story_id: number | null }) {
    return this.http.post(Constants.host + 'invitemember', payload);
  }


  getUsersByQuery(query: string) {
    return this.http.get<HttpResponseInterface<{ users: UserInterface[] }>>(Constants.host + 'userdata?query=' + query).pipe(map(res => res.data.users));
  }

  getTeamByQuery(query: string, storyId: number) {
    return this.http.get<HttpResponseInterface<{ users: UserInterface[] }>>(Constants.host + 'userdatabystory/' + storyId + '?query=' + query).pipe(map(res => res.data.users));
  }

  getUserRole() {
    return this.http.get<HttpResponseInterface>(Constants.host + 'useroles').pipe(map(res => res.data));
  }

  setUser(payload: Partial<UserInterface>): Observable<HttpResponseInterface> {
    return this.http.post<HttpResponseInterface>(Constants.host + 'userprofile', payload).pipe(tap(res => {
      this.updateUserEverywhere();
    }));
  }

  setUserRole(data) {
    sessionStorage.setItem(UserService.UserRole, JSON.stringify(data));
  }

  getRoleType() {
    return sessionStorage.getItem('roleType') ? sessionStorage.getItem('roleType') : 'not_determined';
  }

  getCurrentUserRole() {
    return JSON.parse(sessionStorage.getItem(UserService.UserRole));
  }

}
