import {Component, OnInit} from '@angular/core';
import {UserService} from '../../services/user.service';
import {UserInterface} from '../../models/interfaces/user.interface';
import {CustomValidators} from '@parth181195/ngx-validators';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {NbToastrService} from '@nebular/theme';

@Component({
  selector: 'app-profile-page',
  templateUrl: './profile-page.component.html',
  styleUrls: ['./profile-page.component.sass']
})
export class ProfilePageComponent implements OnInit {
  userData: UserInterface;
  userDataForm: FormGroup;
  isUpdating = false;

  constructor(private user: UserService, private toastrService: NbToastrService) {

    this.userDataForm = new FormGroup({
      // @ts-ignore
      name: new FormControl('', [Validators.required, CustomValidators.alphabet]),
      // @ts-ignore
      email: new FormControl('', [Validators.required, CustomValidators.email])
    });

    this.user.userSubject().subscribe(res => {
      this.userData = res;
      this.userDataForm.get('name').setValue(this.userData.name);
      this.userDataForm.get('email').setValue(this.userData.email);
      this.userDataForm.get('email').disable();
    });


  }

  ngOnInit() {
    this.user.getUser();
  }

  saveData() {
    if (this.userDataForm.valid) {
      this.isUpdating = true;
      const payload = {...this.userDataForm.value};
      payload.user_id = this.userData.id;
      this.user.setUser(payload).subscribe(res => {

        if (!res.error) {
          this.isUpdating = false;
          // this.toastrService.success('', 'Profile Updated');
        }
      });
    }
  }

  toggle($event: boolean) {

  }

  isUploading(event) {
    if (event) {
      this.userData.profile_image = event.url;
    }
  }
}
