/* tslint:disable:no-string-literal */
import { Injectable } from '@angular/core';
import { HttpClient, HttpEventType, HttpRequest, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable, Subject, pipe } from 'rxjs';
import { Constants } from '../utils/utils';
import { map } from 'rxjs/operators';
import { HttpResponseInterface } from '../models/interfaces/http-response.interface';
import { StoryService } from '../services/story.service';
@Injectable({
  providedIn: 'root'
})
export class UploadService {

  headers = new HttpHeaders({
    'story' : this.story.getStoryId() ? this.story.getStoryId().toString() : '' ,
    'role' : this.getUserRole()
  });

  constructor(private http: HttpClient, public story: StoryService) {
  }

  getUserRole() {
    let role = JSON.parse(sessionStorage.getItem('user_role'));
    return role && role['id'] ? role['id'].toString() : '';
  }

  public upload(files: Set<File>): FileUploadStatusInterface[] {
    const status: FileUploadStatusInterface[] = [];

    files.forEach((file) => {
      // create a new multipart-form for every file
      const formData: FormData = new FormData();
      formData.append('assets[]', file, file.name);
      // create a http-post request and pass the form
      // tell it to report the upload progress
      const req = new HttpRequest('POST', Constants.host + 'asset', formData, {
        reportProgress: true,
        headers: this.headers
      });
      // create a new progress-subject for every file
      const progress = new Subject<number>();
      const response = new Subject<number>();

      // send the http-request and subscribe for progress-updates
      this.http.request(req).subscribe(event => {
        if (event.type === HttpEventType.UploadProgress) {

          // calculate the progress percentage
          const percentDone = Math.round(100 * event.loaded / event.total);

          // pass the percentage into the progress-stream
          progress.next(percentDone);
        } else if (event instanceof HttpResponse) {

          // Close the progress-stream if we get an answer form the API
          // The upload is complete

          response.next(event.body['data'][0]);
          progress.complete();
        }
      });
      // Save every progress-observable in a map of all observables
      status.push({
        mimeType: file.type,
        name: file.name,
        progressObs: progress,
        responseObs: response,
      });
    });
    // console.log(status);
    // return the map of progress.observables
    return status;
  }

  public uploadProfileImage(files: Set<File>): Observable<ProfileImageInterface>{
    let sub = new Subject<ProfileImageInterface>();
    const formData: FormData = new FormData();
    files.forEach((file) => {
      formData.append('profile_image', file, file.name);
    });
    this.http.post<HttpResponseInterface<ProfileImageInterface>>(Constants.host + 'changeprofile', formData).subscribe(res=>{
      sub.next(res.data);
    });
    return sub;
  }
}

export interface FileUploadStatusInterface {
  name: string;
  mimeType: string;
  progressObs: Observable<number>;
  responseObs?: Observable<number>;
  id?: number;
}

export interface ProfileImageInterface {
  url: string;
  image_name: string;
}
