import {Injectable} from '@angular/core';
import {HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {catchError, map} from 'rxjs/operators';
import {HttpResponseInterface} from '../models/interfaces/http-response.interface';
import {HttpStatusEnum} from '../models/enums/http-status.enum';
import {TokenService} from './token.service';

@Injectable()
export class Interceptor implements HttpInterceptor {
  constructor(private token: TokenService) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    request.headers.delete('x-ijt');
    let customReq;
    customReq = request.clone({headers: request.headers.set('Authorization', 'Bearer' + this.token.getToken())});
    if (this.token.getToken()) {
    } else {
      customReq = request;
    }
    return next.handle(customReq).pipe(catchError((res) => {

      if (request.headers.keys().indexOf('skip-token-check') !== -1 && res.status === 401) {

        return this.token.refreshToken().pipe(map(() => {
          customReq = customReq.clone({headers: request.headers.set('Authorization', 'Bearer' + this.token.getToken())});
          return customReq;
        }));
      }
      return next.handle(customReq);
    }), map((event) => {
        if (event instanceof HttpResponse) {
          return event.clone({
            body: {
              data: event.body.data,
              status_code: event.status,
              progress: HttpStatusEnum.completed,
              error: event.body.error
            } as HttpResponseInterface
          });
        }
        if (event instanceof HttpErrorResponse) {
          if (event.status === 401) {
          }
          return event;
        }
        return event;
      }
    ));
  }
}
