import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges} from '@angular/core';
import {FileUploadStatusInterface} from '../../services/upload.service';

@Component({
  selector: 'app-asset-data-chip',
  templateUrl: './asset-data-chip.component.html',
  styleUrls: ['./asset-data-chip.component.sass']
})
export class AssetDataChipComponent implements OnInit, OnChanges {
  @Input() file: FileUploadStatusInterface;
  @Output() onRemoveFile: EventEmitter<FileUploadStatusInterface> = new EventEmitter();

  constructor() {
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.file) {
      this.file = changes.file.currentValue;
    }
  }

  close() {
    this.onRemoveFile.emit(this.file);
  }
}
