import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {SlateService} from '../../services/slate.service';
import {CeSlateInterface} from '../../models/interfaces/ce-slate.interface';
import {tap} from 'rxjs/operators';
import {StoryService} from '../../services/story.service';
import {ToastService} from '../../services/toast.service';
import {AuthService} from '../../services/auth.service';
import {NbToastrService} from '@nebular/theme';
import {CommentsInterface, CommentsService} from '../../services/comments.service';

@Component({
  selector: 'app-ce-slate-details',
  templateUrl: './ce-slate-details.component.html',
  styleUrls: ['./ce-slate-details.component.sass']
})
export class CeSlateDetailsComponent implements OnInit {
  isLoading = true;
  slateData: CeSlateInterface;
  slateId: number;
  approvePage: boolean;
  storyData: any;
  milestoneData: any;
  formDisabled = true;
  tags = false;
  roleType;

  // storyMilestoneData = {story_id: 0, milestone:[], status_deadline:[]};

  constructor(private route: ActivatedRoute, private auth: AuthService, private slate: SlateService, private toast: ToastService, private story: StoryService, private toastr: NbToastrService, private router: Router, private commentsService: CommentsService) {
    this.route.params.subscribe(res => {
      this.slateId = res.id;
      this.approvePage = this.router.url.includes('approve');
      this.slate.getSlate(this.slateId).subscribe(res => {
        this.isLoading = false;
        this.slateData = res;
      });
      this.commentsService.emptyCommentsArr();
    });

    this.roleType = this.auth.getRoleType();

  }

  chooseFormatIcon() {
    switch (this.slateData.format) {
      case 'Video Essay':
        return 'activity-outline';
      case 'Photo Essay':
        return 'image-outline';
      default:
        return 'file-outline';
    }
  }

  tagsExist() {
    this.tags = true;
  }

  changeStateToNeedsWork() {
    this.story.changeStatus({story_id: this.slateId, story_status: 'needs_work'}).subscribe(res => {
      this.toastr.success('', 'story status updated', {icon: ''});
    }, err => {
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  changeStateToApprove() {
    this.story.changeStatus({story_id: this.slateId, story_status: 'approved'}).subscribe(res => {
      this.toastr.success('', 'story status updated', {icon: ''});
      this.router.navigateByUrl(`approve-slate/${this.slateId}`).then(() => {
      });
    }, err => {
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  cancel() {
    this.router.navigate(['slate']);
  }

  storyMilestoneData(event) {
    this.milestoneData = event;
  }

  createStoryWorkspace() {
    let _this = this;
    this.story.updateMilestones(this.milestoneData, this.slateId).subscribe(res => {
      _this.story.updateApprovedStory(_this.storyData, this.slateId).subscribe(res => {
        _this.router.navigateByUrl(`story/${this.slateId}`);
      }, err => {
        this.toast.showToast('', err.error.cause, 'danger', '');
      });
    }, err => {
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  storyDataFunc(event) {
    this.storyData = event;
  }

  isFormValid(event) {
    this.formDisabled = !event;
  }

  ngOnInit() {
  }

}
