import { Injectable, OnDestroy } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { AuthStatusInterface } from '../models/interfaces/auth-status.interface';
import { AuthStatusEnum } from '../models/enums/auth-status.enum';
import { UserRoleEnum } from '../models/enums/auth-status.enum';
import { GoogleAuthService } from 'ng-gapi';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Constants } from '../utils/utils';
import { HttpResponseInterface } from '../models/interfaces/http-response.interface';
import { tap } from 'rxjs/operators';
import GoogleUser = gapi.auth2.GoogleUser;
import { SignUpInputInterface, SignUpResponseInterface } from '../models/interfaces/signup.interface';
import { TokenService } from './token.service';
import { UserService } from './user.service';
import { MessagingService } from './messaging.service';


@Injectable({
  providedIn: 'root'
})

export class AuthService implements OnDestroy {
  public static SESSION_STORAGE_KEY = 'accessToken';
  private user: GoogleUser;
  httpOptions = {
    headers: new HttpHeaders({
      'skip-token-check': 'true',
    })
  };
  private authState = new BehaviorSubject<AuthStatusInterface>({
    isLoading: true,
    status: AuthStatusEnum.notDetermined,
    userRole: this.getRoleType()
  });

  constructor(private googleAuth: GoogleAuthService, private msg: MessagingService, private http: HttpClient, private token: TokenService, private userService: UserService) {
    const isLoggedIn = localStorage.getItem(AuthService.SESSION_STORAGE_KEY);

    if (isLoggedIn) {
      this.authState.next({ status: AuthStatusEnum.loggedIn, isLoading: false, userRole: this.getRoleType() });
    } else {
      this.authState.next({ status: AuthStatusEnum.loggedOut, isLoading: false, userRole: this.getRoleType() });
    }
  }

  ngOnDestroy(): void {
  }

  pushAuthState(state: AuthStatusInterface) {
    this.authState.next(state);
  }

  getAuthState() {
    return this.authState;
  }

  logout() {
    this.authState.next({ status: AuthStatusEnum.loggedOut, isLoading: false, userRole: this.getRoleType() });
    localStorage.removeItem(
      AuthService.SESSION_STORAGE_KEY
    );
    sessionStorage.clear();
    this.googleAuth.getAuth()
      .subscribe((auth) => {
        auth.signOut();
      });

  }

  signUp(payload: SignUpInputInterface): Observable<HttpResponseInterface<SignUpResponseInterface>> {
    return this.http.post<HttpResponseInterface<SignUpResponseInterface>>(Constants.host + 'signup', payload, this.httpOptions).pipe(tap(res => {
      if (res.data) {
        this.token.storeToken(res.data.token);
        this.userService.getUser();
        this.authState.next({ isLoading: false, status: AuthStatusEnum.loggedIn, userRole: this.getRoleType() });
      }
    }));
  }

  getOtp(email: string) {
    return this.http.post<HttpResponseInterface>(Constants.host + 'login', { email }, this.httpOptions);
  }

  checkOtp(email: string, otp: number) {
    return this.http.post<HttpResponseInterface>(Constants.host + 'verifiedotp', { email, otp }, this.httpOptions).pipe(tap(res => {
      if (res.data.is_registered) {
        this.token.storeToken(res.data.token);
        this.userService.getUser();
        this.msg.requestPermission();
        this.authState.next({ isLoading: false, status: AuthStatusEnum.loggedIn, userRole: this.getRoleType() });
      }
    }));
  }

  loginWithGoogle(): Observable<AuthStatusInterface> {
    const sub = new BehaviorSubject<AuthStatusInterface>({
      isLoading: true,
      status: AuthStatusEnum.notDetermined,
      userRole: 'not_determined',
    });
    this.googleAuth.getAuth()
      .subscribe((auth) => {
        auth.signIn().then(res => {
          this.userService.getUser();
          this.authState.next({ isLoading: true, status: AuthStatusEnum.notDetermined, userRole: this.getRoleType() });
          this.signInSuccessHandler(res as GoogleUser).subscribe(response => {
            console.log(this.user)
            this.authState.next({ isLoading: false, status: AuthStatusEnum.loggedIn, userRole: this.getRoleType() });
            sub.next({ isLoading: false, status: AuthStatusEnum.loggedIn, userRole: this.getRoleType(), token: res.getAuthResponse().id_token });
            sub.complete();
            this.msg.requestPermission();
          }, error => {
            this.authState.next({ isLoading: false, status: AuthStatusEnum.loggedOut, userRole: this.getRoleType() });
            sub.next({ isLoading: false, status: AuthStatusEnum.loggedOut, userRole: this.getRoleType() });
            sub.complete();
          });
        }, (err) => {
          sub.next({ isLoading: false, status: AuthStatusEnum.loggedOut, userRole: this.getRoleType() });
          sub.complete();
        });
      });
    return sub;
  }

  getGoogleToken() {
    const sub = new Subject<any>();
    this.googleAuth.getAuth()
      .subscribe((auth) => {
        auth.signIn().then((res: GoogleUser) => {
          let user = res as GoogleUser;
          sub.next(user.getAuthResponse().access_token)
        }, (err) => {
        });
      });
    return sub;
  }
  private signInSuccessHandler(res: GoogleUser) {
    this.user = res;
    return this.http.post<HttpResponseInterface>(Constants.host + 'login/google', { idtoken: res.getAuthResponse().id_token }, this.httpOptions)
      .pipe(tap(next => {
        localStorage.setItem(
          AuthService.SESSION_STORAGE_KEY, next.data.token
        );
      }));
  }

  setRoleType(data) {
    sessionStorage.setItem('roleType', data);
  }

  getRoleType() {
    return sessionStorage.getItem('roleType') ? sessionStorage.getItem('roleType') : 'not_determined';
  }

}



