import {Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {CommentsInterface} from '../../../services/comments.service';
import * as moment from 'moment';
import * as _ from 'lodash';
import {UserInterface} from '../../../models/interfaces/user.interface';

@Component({
  selector: ' app-comment-entry',
  templateUrl: './comment-entry.component.html',
  styleUrls: ['./comment-entry.component.sass']
})
export class CommentEntryComponent implements OnInit, OnChanges {
  @Input() data: Partial<CommentsInterface> = {};
  @Input() type = 'story';
  @Input() id;
  @Input() story_id;
  @Input() discussions;
  date;
  msgArr = {msg: []};
  isReplying = false;

  constructor() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    let now = moment();
    if (changes.data) {
      this.data = changes.data.currentValue;
      this.date = moment(this.data.created_at).calendar(new Date());
    }
  }

  ngOnInit() {
  }

  msgFormatter(data: Partial<CommentsInterface>) {
    const placesToBold = [];
    if (data.mentions && data.mentions.data && data.comment_text) {
      let msg = data.comment_text.split(/<<([0-9]+?)>>/gmi);
      data.mentions.data.map((user, index) => {
        if (msg.indexOf(`${user.id}`) !== -1) {
          placesToBold.push(msg.indexOf(`${user.id}`));
          msg[msg.indexOf(`${user.id}`)] = '@' + user.name;
        }
      });
      return {msgArr: msg, bold: placesToBold};
    } else {
      return {msgArr: [''], bold: []};
    }

  }

}
