import {Component, OnInit, Output, EventEmitter, OnDestroy} from '@angular/core';
import {UserDialogService} from '../../elements/user-dialog/user-dialog.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {UserInterface} from '../../models/interfaces/user.interface';
import {UserService} from '../../services/user.service';
import {EnumService} from '../../services/enum.service';
import {StoryService} from '../../services/story.service';
import {debounceTime} from 'rxjs/operators';
import {NbToastrService} from '@nebular/theme';
import {ActivatedRoute} from '@angular/router';
import {SubSink} from 'subsink';

@Component({
  selector: 'app-slate-approval-details',
  templateUrl: './slate-approval-details.component.html',
  styleUrls: ['./slate-approval-details.component.sass']
})
export class SlateApprovalDetailsComponent implements OnInit, OnDestroy {
  approvalSlateForm: FormGroup;
  author = new FormControl();
  editor = new FormControl();
  publishDate = new FormControl();
  filteredOptionsAuthor = [];
  filteredOptionsEditor = [];
  filteredOptionsMilestones;
  slateId: number;
  stage: FormGroup;
  add = 1;
  stagesArr: Array<any> = [];
  newMilestone: any;
  @Output() storyData = new EventEmitter<any>();
  @Output() storyMilestoneData = new EventEmitter<any>();
  @Output() allFieldsEntered = new EventEmitter<any>();
  authorRole: number;
  editorRole: number;
  formattedPublishDate: string;
  showEmptyChange = true;
  data: { story_id: number; assign_id: any; role_id: any; publish_date: string; story_status: string; };
  private subs = new SubSink();
  arrayChanged = false;

  constructor(private dialog: UserDialogService, private user: UserService, private toastr: NbToastrService, private route: ActivatedRoute, private enumService: EnumService, private story: StoryService) {
    this.route.params.subscribe(res => {
      this.slateId = res.id;
      this.user.getUsersByQuery('').subscribe(users => {
        this.filteredOptionsAuthor = users;
        this.filteredOptionsEditor = users;
      });
      this.story.setStoryId(this.slateId);
    });

    this.approvalSlateForm = new FormGroup({
      assign_id: new FormControl([], [Validators.required]),
      role_id: new FormControl([], [Validators.required]),
      publish_date: new FormControl('', [Validators.required])
    });

    this.stage = new FormGroup({
      newStage: new FormControl('', [Validators.required]),
      date: new FormControl('', [Validators.required])
    });
  }

  ngOnInit() {

    this.subs.sink = this.author.valueChanges.subscribe(res => {
      this.getApprovalSlateValue(false);
      this.user.getUsersByQuery(res).subscribe(users => {
        this.filteredOptionsAuthor = users;
      });
    });

    this.subs.sink = this.editor.valueChanges.subscribe(res => {
      this.getApprovalSlateValue(false);
      this.user.getUsersByQuery(res).subscribe(users => {
        this.filteredOptionsEditor = users;
      });
    });

    this.subs.sink = this.stage.get('newStage').valueChanges.subscribe(res => {
      if (res && res.length) {
        this.story.getMilestones(res, this.slateId).subscribe(res => {
          this.filteredOptionsMilestones = res;
        });
      }
    });

    this.subs.sink = this.stage.valueChanges.subscribe(res => {
      if (this.stage.valid) {
        this.arrayChanged = false;
        this.checkStage(true);
      }
    });

    this.subs.sink = this.publishDate.valueChanges.subscribe(res => {
      this.formattedPublishDate = this.formatDate(res);
      this.emitFormData();
    });

    this.subs.sink = this.enumService.getRoles().subscribe(res => {
      // this.roles = res;
      let _this = this;
      res.forEach(element => {
        if (element.display === 'author') {
          _this.authorRole = parseInt(element.id);
        } else if (element.display === 'editor') {
          _this.editorRole = parseInt(element.id);
        }
      });
    });
  }

  dateInFocus(event, item, index) {
    this.stagesArr[index].date = event.target.value;
    this.changeArrToMilestone();
  }

  timelineHandle(value) {
    return value.id ? value.milestone_title : value;
  }

  userHandle(value: UserInterface) {
    return value.id ? value.name : value;
  }

  formatDate(date: string) {
    let publishDate = new Date(date);
    return publishDate.getFullYear() + '-' + ((publishDate.getMonth() + 1) < 10 ? '0' + (publishDate.getMonth() + 1) : (publishDate.getMonth() + 1)) + '-' + ((publishDate.getDate()) < 10 ? '0' + (publishDate.getDate()) : (publishDate.getDate()));
  }

  onSelectionChange(event, type) {
    if (event) {
      if (type === 'author') {
        this.approvalSlateForm.get('assign_id').value[0] = event.id;
        this.approvalSlateForm.get('role_id').value[0] = this.authorRole;
      } else {
        this.approvalSlateForm.get('assign_id').value[1] = event.id;
        this.approvalSlateForm.get('role_id').value[1] = this.editorRole;
      }
    }
    this.emitFormData();
  }

  openInviteMemberDialog(e, type) {
    e.stopPropagation();
    if (type === 'author') {
      this.approvalSlateForm.get('assign_id').value[0] = this.author.value;
      this.approvalSlateForm.get('role_id').value[0] = this.authorRole;
    } else {
      this.approvalSlateForm.get('assign_id').value[1] = this.editor.value;
      this.approvalSlateForm.get('role_id').value[1] = this.editorRole;
    }
    this.emitFormData();
  }

  getApprovalSlateValue(checkValue) {
    if ((this.author.value && !checkValue) || (checkValue && this.author.value && !this.approvalSlateForm.get('assign_id').value[0])) {
      this.approvalSlateForm.get('assign_id').value[0] = typeof this.author.value === 'object' ? this.author.value.id : this.author.value;
      this.approvalSlateForm.get('role_id').value[0] = this.authorRole;
    }
    if ((this.editor.value && !checkValue) || (checkValue && this.editor.value && !this.approvalSlateForm.get('assign_id').value[1])) {
      this.approvalSlateForm.get('assign_id').value[1] = typeof this.editor.value === 'object' ? this.editor.value.id : this.editor.value;
      this.approvalSlateForm.get('role_id').value[1] = this.editorRole;
    }
  }

  toggle(checked, index) {
    this.stagesArr[index].checked = checked;
    this.changeArrToMilestone();
  }

  checkStage(val: any) {
    if (val === true) {
      this.stage.value.checked = true;
      this.filteredOptionsMilestones.forEach(value => {
        if (value.milestone_title === this.stage.value.newStage) {
          this.stage.value.stage = value.id;
        }
      });
      this.stagesArr.push(this.stage.value);
      this.changeArrToMilestone();
      this.showEmptyChange = false;
      this.arrayChanged = true;
    }
  }

  addStage() {
    if (this.arrayChanged) {
      this.stage.reset();
      this.stage.get('newStage').setValue('');
      this.showEmptyChange = true;
    }
  }

  changeArrToMilestone() {
    let milestones = [];
    let status_deadline = [];
    this.stagesArr.forEach(element => {
      if (element.checked) {
        if (element.stage) {
          milestones.push(element.stage);
        } else {
          milestones.push(element.newStage);
        }
        status_deadline.push(this.formatDate(element.date));
      }
    });
    if (milestones.length) {
      let data = {story_id: this.slateId, milestones: milestones, status_deadline: status_deadline};
      this.storyMilestoneData.emit(data);
      this.checkRequiredFields();
    }
  }

  emitFormData() {
    this.getApprovalSlateValue(true);
    this.data = {
      story_id: this.slateId,
      assign_id: this.approvalSlateForm.get('assign_id').value,
      role_id: this.approvalSlateForm.get('role_id').value,
      publish_date: this.publishDate.value ? this.formattedPublishDate : '',
      story_status: 'story'
    };
    this.storyData.emit(this.data);
    this.checkRequiredFields();
  }

  checkRequiredFields() {
    let checkStatus = true;
    for (let key in this.data) {
      if (!(this.data[key].length)) {
        checkStatus = false;
      }
    }
    let isMilestoneSet = this.stagesArr.filter(function(e) {
      return e.checked === true;
    });

    this.allFieldsEntered.emit(checkStatus && isMilestoneSet.length);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }
}
