import {Component, OnInit, ViewChild, AfterViewInit} from '@angular/core';
import {Location} from '@angular/common';
import {ActivatedRoute, Router} from '@angular/router';
import {StoryInterface} from '../../models/interfaces/story.interface';
import {StoryService} from '../../services/story.service';
import {UserService} from '../../services/user.service';
import {CommentsService} from '../../services/comments.service';
import {ToastService} from '../../services/toast.service';
import {AuthService} from '../../services/auth.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {NbDialogService} from '@nebular/theme';
import {AddAssignmentDialogComponent} from '../../elements/add-assignment-dialog/add-assignment-dialog.component';
import {NbPopoverDirective} from '@nebular/theme';
import {InviteTeamMemberComponent} from '../../elements/invite-team-member/invite-team-member.component';

@Component({
  selector: 'app-story-workspace',
  templateUrl: './story-workspace.component.html',
  styleUrls: ['./story-workspace.component.sass']
})
export class StoryWorkspaceComponent implements OnInit, AfterViewInit {
  // @ViewChild(NbPopoverDirective) addAssignmentPopover: NbPopoverDirective;
  popoverInactive = false;
  isLoading = true;
  isLoadingTab = true;
  storyData: StoryInterface;
  storyId: number;
  approvePage: boolean;
  milestoneData: any;
  formDisabled = true;
  storyMilestones = [];
  tags = false;
  activeTab = 0;
  taskList: any = [];
  draftList: any = [];
  teamList: any = [];
  discussionCount = 0;
  assetList: any = [];
  assignmentDetails: FormGroup;
  draftDetails: FormGroup;
  assignmentData: any;
  editorMember: any = [];
  contributorMember: any = [];
  authorMember: any = [];
  latestDraft: any;
  otherDrafts: any = [];
  taskStatusMilestones: any;
  allTasks: { name: 'Completed' | 'Overdue' | 'Today' | 'Tomorrow' | 'Coming Up' | 'No Deadline', data: any[] }[] = [
    {name: 'Completed', data: []},
    {name: 'Overdue', data: []},
    {name: 'Today', data: []},
    {name: 'Tomorrow', data: []},
    {name: 'Coming Up', data: []},
    {name: 'No Deadline', data: []}
  ];
  roleType = '';
  currentUser: any;
  max_version: any;

  // storyMilestoneData = {story_id: 0, milestone:[], status_deadline:[]};

  constructor(private location: Location, private route: ActivatedRoute, private dialogService: NbDialogService, private story: StoryService, public toast: ToastService, private router: Router, private commentsService: CommentsService, private auth: AuthService, private user: UserService) {

    this.route.params.subscribe(res => {

      this.storyId = res.id;
      this.story.setStoryId(this.storyId);
      this.route.queryParams.subscribe(params => {
        if (params.tab) {
          this.activeTabClicked(parseInt(params.tab));
        } else {
          this.activeTabClicked(0);
        }
      });
      this.story.getStory(this.storyId).subscribe(res => {
        this.isLoading = false;
        this.storyData = res;
        this.story.getStoryMilestones().subscribe(res => {
          this.storyMilestones = res;
        }, err => {
          this.isLoading = false;
          this.toast.showToast('', err.error.cause, 'danger', '');
        });
        this.story.getTaskStatusList().subscribe(res => {
          this.taskStatusMilestones = res;
        }, err => {
          this.isLoading = false;
          this.toast.showToast('', err.error.cause, 'danger', '');
        });
        this.roleType = this.auth.getRoleType();
      }, err => {
        this.isLoading = false;
        this.toast.showToast('', err.error.cause, 'danger', '');
        this.router.navigate(['/no-access']);
      });

      // this.activeTab = 0;
      this.getTasks();

      this.draftDetails = new FormGroup({
        // @ts-ignore
        draft_url: new FormControl('', Validators.required),
        summary: new FormControl('', Validators.required),
        title: new FormControl('', Validators.required)
      });

      this.assignmentDetails = new FormGroup({
        // @ts-ignore
        title: new FormControl('', Validators.required)
      });

    });
  }

  ngOnInit() {

  }

  ngAfterViewInit() {
  }

  activeTabClicked(tab) {
    this.location.replaceState(this.location.path().split('?')[0], 'tab=' + tab);
    this.activeTab = tab;
    switch (this.activeTab) {
      case 0: {
        this.isLoadingTab = true;
        this.getTasks();
        break;
      }
      case 1: {
        this.isLoadingTab = true;
        this.getDrafts();
        break;
      }
      case 2: {
        this.isLoadingTab = true;
        this.getAssets();
        break;
      }
      case 3: {
        this.isLoadingTab = true;
        this.getDiscussions();
        break;
      }
      case 4: {
        this.isLoadingTab = true;
        this.getTeam();
        break;
      }
      default:
        break;
    }
  }

  getTasks() {
    this.taskList = [];
    this.story.getTaskList(this.storyId).subscribe(res => {
      this.taskList = res['tasks'];
      this.arrangeTask();
      this.isLoadingTab = false
    }, err => {
      this.isLoadingTab = false;
      this.isLoading = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  getDrafts() {

    this.draftList = [];
    this.story.getStoryDrafts(this.storyId).subscribe(res => {
      this.draftList = res['draft'];
      this.isLoadingTab = false;
      this.divideDrafts();
    }, err => {
      this.isLoadingTab = false;
      this.isLoading = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  getTeam() {
    this.teamList = [];
    this.story.getStoryMembers(this.storyId).subscribe(res => {
      this.teamList = res['data'];
      this.divideTeamMembers();
      this.isLoadingTab = false
    }, err => {
      this.isLoadingTab = false;
      this.isLoading = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  getAssets() {
    this.story.getStory(this.storyId).subscribe(res => {
      this.storyData = res;
      this.isLoadingTab = false
    }, err => {
      this.isLoadingTab = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  getDiscussions() {
  }

  arrangeTask() {
    if (this.allTasks) {
      this.allTasks.forEach(type => {
        type['data'] = [];
      });
    }
    this.taskList.forEach(task => {
      let deadline = Math.floor((Date.parse(task['deadline'])) / 86400000);
      let todaysdate = Math.floor((Date.now()) / 86400000);
      if (task['task_status'] === 'completed' || task['task_status'] === 'Completed') {
        this.allTasks[0].data.push(task);
      } else {
        if (task['deadline']) {
          if (deadline - todaysdate < -1) {
            this.allTasks[1].data.push(task);
          } else if (deadline - todaysdate === 0) {
            this.allTasks[3].data.push(task);
          } else if (deadline - todaysdate === -1) {
            this.allTasks[2].data.push(task);
          } else {
            this.allTasks[4].data.push(task);
          }
        } else {
          this.allTasks[5].data.push(task);
        }
      }
    });
  }

  divideTeamMembers() {
    this.editorMember = [];
    this.authorMember = [];
    this.contributorMember = [];
    this.teamList.forEach(member => {
      switch (member.role) {
        case 'commisioning_editor':
        case 'publisher':
        case 'chief_editor':
        case 'ats_core':
        case 'editor':
        case 'producer':
          this.editorMember.push(member);
          break;
        case 'contributor':
        case 'reader':
          this.contributorMember.push(member);
          break;
        case 'author':
        case 'co_author':
          this.authorMember.push(member);
          break;
        default:
          this.contributorMember.push(member);
      }
    });
  }

  divideDrafts() {
    if (this.draftList.length) {
      this.max_version = Math.max.apply(Math, this.draftList.map(function(o) {
        return o.draft_version;
      }));
      this.latestDraft = this.draftList.filter(draft => draft['draft_version'] === this.max_version);
      this.otherDrafts = this.draftList.filter(draft => draft['draft_version'] < this.max_version);
      this.otherDrafts = this.otherDrafts.reverse();
    }
  }

  tagsExist() {
    this.tags = true;
  }

  chooseFormatIcon() {
    switch (this.storyData.format) {
      case 'Video Essay':
        return 'activity-outline';
      case 'Photo Essay':
        return 'image-outline';
      default:
        return 'file-outline';
    }
  }

  deleteTask(item) {
    this.story.deleteTask({task_id: item.id}, this.storyId).subscribe(res => {
      this.getTasks();
    }, err => {
      this.isLoading = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  changeMilestone(item, milestone, typeindex, dataindex) {
    let payload = {
      task_id: item ? item.id : '',
      task_status_id: milestone.id
    };
    this.story.changeTaskStatus(payload, this.storyId).subscribe(res => {
      this.allTasks[typeindex].data[dataindex].task_status = milestone.status_title;
    }, err => {
      this.isLoading = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  checkMilestone(milestone, status) {
    return milestone === status;
  }

  openAddAssignment(taskData?, type?, name?) {
    if (name && name === 'Completed' && this.roleType !== 'editor') {
      return;
    }
    if (this.roleType === 'ats_core') {
      return;
    }
    this.dialogService.open(AddAssignmentDialogComponent,
      {
        context: {title: this.assignmentDetails.get('title').value, storyData: this.storyData, taskData: taskData, type: type}
      })
      .onClose.subscribe(result => {
      this.story.getTaskList(this.storyId).subscribe(res => {
        this.taskList = res['tasks'];
        this.arrangeTask();
      }, err => {
        this.isLoading = false;
        this.toast.showToast('', err.error.cause, 'danger', '');
      });
    });
  }

  inviteTeamMember() {
    this.dialogService.open(InviteTeamMemberComponent,
      {
        hasBackdrop: true,
        context: {storyId: this.storyId}
      })
      .onClose.subscribe(result => {
      this.story.getStoryMembers(this.storyId).subscribe(res => {
        this.teamList = [];
        this.teamList = res['data'];
        this.divideTeamMembers();
      }, err => {
        this.isLoading = false;
        this.toast.showToast('', err.error.cause, 'danger', '');
      });
    });
  }

  getAssignmentData(result) {
  }

  addAssignmentApi() {
    let payload = {
      story_id: this.storyId,
      title: this.assignmentDetails.get('title').value,
      task_status_id: 1
    };
    // this.NbPopoverDirective.hide();
    this.popoverInactive = true;
    this.assignmentDetails.reset();
    this.story.createTask(payload, this.storyId).subscribe(res => {
      this.story.getTaskList(this.storyId).subscribe(res => {
        this.toast.showToast('', 'Assignment Added', 'success', '');
        this.taskList = res['tasks'];
        this.arrangeTask();
      }, err => {
        this.isLoading = false;
        this.toast.showToast('', err.error.cause, 'danger', '');
      });
    }, err => {
      this.isLoading = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  addDraftApi() {
    this.auth.getGoogleToken().subscribe(token => {
      console.log(token);
      if (token) {
        this.draftDetails.value.draft_version = this.max_version ? this.max_version + 1 : 1;
        this.story.createDraft(this.draftDetails.value, this.storyId, token).subscribe(res => {
          this.story.getStoryDrafts(this.storyId).subscribe(res => {
            this.draftDetails.reset();
            this.toast.showToast('', 'Draft Added', 'success', '');
            this.draftList = res['draft'];
            this.divideDrafts();
          }, err => {
            this.isLoading = false;
            this.toast.showToast('', err.error.cause, 'danger', '');
          });
        }, err => {
          this.toast.showToast('', err.error.cause, 'danger', '');
        });
      }
    });
  }

  checkPublishDateStatus(item): string {
    var publishDate = (Date.parse(item));
    var todaysDate = (Date.now());
    // To ignore time
    // return (Math.floor(publishDate / 86400000) < Math.floor(todaysDate / 86400000))? "not on": "on";

    // when time is 00:00:00
    return (publishDate < todaysDate) ? 'not on' : 'on';
  }

  formatMilestone(milestone) {
    return milestone ? ((milestone.split('_')).filter(val => val[0].toUpperCase())).join(' ') : '';
  }

  storyMilestoneData(event) {
    this.milestoneData = event;
  }

  storyDataFunc(event) {
    this.storyData = event;
  }

  isFormValid(event) {
    this.formDisabled = !event;
  }

  changeStoryMilestone(story_milestone_id, milestone_id) {
    let payload = {
      milestone_status_id: milestone_id,
      story_milestone_id: story_milestone_id
    };
    this.story.changeStoryMilestoneStatus(payload, this.storyId).subscribe(res => {
      this.story.getStory(this.storyId).subscribe(res => {
        this.isLoading = false;
        this.storyData = res;
      }, err => {
        this.isLoading = false;
        this.toast.showToast('', err.error.cause, 'danger', '');
      });
    }, err => {
      this.isLoading = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

}

