import {Component, OnInit} from '@angular/core';
import {SubSink} from 'subsink';
import {PublishedStoriesService} from '../../services/published-stories.service';
import {StoryInterface} from '../../models/interfaces/story.interface';
import {StoryGroupInterface} from '../../models/interfaces/story-group.interface';

@Component({
  selector: 'app-published-stories',
  templateUrl: './published-stories.component.html',
  styleUrls: ['./published-stories.component.sass']
})
export class PublishedStoriesComponent implements OnInit {

  subs = new SubSink();
  loading: boolean = true;
  tempDateArr: any = [];

  tempStoryList: StoryInterface[] = [];
  storyList: StoryGroupInterface[] = [];

  constructor(public stories: PublishedStoriesService) {
  }

  ngOnInit() {
    this.subs.sink = this.stories.getPublishedStory().subscribe(res => {
      this.tempStoryList = res;

      if (this.tempStoryList.length) {
        this.findStoryList();
      }

      this.loading = false;
    });
  }

  findStoryList() {
    this.tempStoryList.forEach(val => {
      let dateFetched = new Date(val.created_at).toDateString();
      let datePresent = false;
      let _this = this;
      this.tempDateArr.forEach(date => {
        if (dateFetched === date) {
          datePresent = true;
        }
      });
      if (!datePresent) {
        this.storyList.push({created_at: dateFetched, story: []});
        this.storyList[(this.storyList.length) - 1].story.push(val);
        this.tempDateArr.push(dateFetched);
      } else {
        this.storyList.forEach(function(element, index) {
          if (element.created_at === dateFetched) {
            _this.storyList[index].story.push(val);
          }
        });
      }
    });
  }
}
