import { Component, OnInit, Input, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { StoryInterface } from '../../models/interfaces/story.interface';
import { TaskInterface } from '../../models/interfaces/task.interface';
import { MilestoneInterface } from '../../models/interfaces/milestone.interface';
import { filter } from 'rxjs/operators';
import { StoryService } from '../../services/story.service';
import { Router } from '@angular/router'

@Component({
  selector: 'app-dash-task-card',
  templateUrl: './dash-task-card.component.html',
  styleUrls: ['./dash-task-card.component.sass']
})
export class DashTaskCardComponent implements OnInit {
  @Input() data: TaskInterface;
  @Input() type: string = "";
  @Input() use: string = "";

  constructor(public story: StoryService, public router: Router) {
  }

  ngOnInit() {
  }

  checkMilestone(milestone, status){
    return milestone === status;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.data) {
      this.data = changes.data.currentValue;
    }
  }

  dotDotDot(s: string) {
    if (s) {
      return s.length > 150 ? s.substr(0, 150) + '...' : s;
    }
    return '';
  }

  formatMilestone(milestone){
    return milestone ? ((milestone.split("_")).filter(val=>val[0].toUpperCase())).join(" ") : ""
  }

  dueDatePassed(item) {
    return Date.parse(item) < Date.now() ? 'danger' : '';
  }

}
