import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Constants} from '../utils/utils';
import {HttpResponseInterface} from '../models/interfaces/http-response.interface';
import {CeSlateInterface} from '../models/interfaces/ce-slate.interface';
import {map} from 'rxjs/operators';
import {StoryCreateInterface} from './story.service';

@Injectable({
  providedIn: 'root'
})
export class SlateService {

  constructor(private http: HttpClient) {
  }

  getStoryId(){
    return sessionStorage.getItem('story_id');
  }

  getUserRole(){
    let role = JSON.parse(sessionStorage.getItem('user_role'));
    return role && role['id'] ? role['id'].toString() : '';
  }

  getCeSlateList() {
    return this.http.get<HttpResponseInterface<{ slates: CeSlateInterface[] }>>(Constants.host + 'slatelist').pipe(map(res => res.data.slates));
  }

  getSlate(storyId) {
    let headers = new HttpHeaders({
      'story' : storyId.toString(),
      'role' : this.getUserRole()
    });
    return this.http.get<HttpResponseInterface<CeSlateInterface>>(Constants.host + 'storydetails', {headers: headers}).pipe(map(res => res.data));
  }
  
  public remove(payload, storyId){
    let headers = new HttpHeaders({
      'story' : storyId.toString() ,
      'role' : this.getUserRole()
    });
    return this.http.post<HttpResponseInterface>(Constants.host + 'removeasset', payload, { headers: headers }).pipe(map(res => res));
  }

}
