import {AfterViewInit, Component, ElementRef, HostListener, OnInit, Renderer2, ViewChild} from '@angular/core';
import {DialogConfig} from '../user-dialog/user-dialog.injector';
import {DialogRef} from '../user-dialog/dialog-ref';
import {FormArray, FormControl, FormGroup} from '@angular/forms';
import {debounceTime, tap} from 'rxjs/operators';
import {UserInterface} from '../../models/interfaces/user.interface';
import {UserService} from '../../services/user.service';

@Component({
  selector: 'app-user-select',
  templateUrl: './user-select.component.html',
  styleUrls: ['./user-select.component.sass']
})
export class UserSelectComponent implements OnInit, AfterViewInit {
  @ViewChild('warpper') warpper: ElementRef;
  searchFormControl = new FormControl();
  userList: UserInterface[];
  loading = false;
  noResult = false;
  userForm: FormGroup = new FormGroup({user: new FormControl('')});
  multi = true;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (this.config.data.evt) {

      let element = this.config.data.evt.target;
      let dist = element.getBoundingClientRect().top +element.getBoundingClientRect().height + 10;
      this.r2.setStyle(this.warpper.nativeElement, 'top', dist + 'px');
      this.r2.setStyle(this.warpper.nativeElement, 'left', element.getBoundingClientRect().x + 'px');
    }
  }

  constructor(public config: DialogConfig, public dialog: DialogRef, private r2: Renderer2, private userService: UserService) {
    this.multi = !!config.data.multi;
    this.searchFormControl.valueChanges.pipe(tap(res => this.loading = true), debounceTime(800)).subscribe(val => {
      this.loading = true;
      this.userList = [];
      if (val !== '') {
        this.userService.getUsersByQuery(val).subscribe(res => {
          this.loading = false;
          this.userList = res;
          this.noResult = res.length === 0;
          if (this.multi) {
            this.userForm = new FormGroup({});
            this.userList.forEach(res => {
              this.userForm.addControl(res.id.toString(), new FormControl(''));
            });
          }
        });
      } else {
        this.loading = false;
        this.userList = undefined;
      }
    });
  }

  ngOnInit() {
  }

  ngAfterViewInit(): void {
    if (this.config.data.evt) {

      let element = this.config.data.evt.target;
      let dist = element.getBoundingClientRect().top + element.getBoundingClientRect().height + 10;
      this.r2.setStyle(this.warpper.nativeElement, 'top', dist + 'px');
      this.r2.setStyle(this.warpper.nativeElement, 'left', element.getBoundingClientRect().x + 'px');
    }
  }

  close() {
    this.dialog.close(this.userForm.value);
  }

  checkIfSelected() {
    if (this.multi) {
      return Object.values(this.userForm.value).indexOf(true) !== -1;
    } else {
      return this.userForm.value.user;
    }
  }
}
