import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree} from '@angular/router';
import {Observable, Subject} from 'rxjs';
import {AuthService} from './auth.service';
import {AuthStatusEnum} from '../models/enums/auth-status.enum';
import {map, tap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthGardService implements CanActivate {

  constructor(private auth: AuthService, private router: Router) {
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean | UrlTree> {
    return this.auth.getAuthState().pipe(map(authState => {
      if (authState.status === AuthStatusEnum.loggedOut) {
        return this.router.createUrlTree(['/login']);
      } else if (authState.status === AuthStatusEnum.loggedIn) {
        let url : string = state.url;
        return this.checkAccess(url, sessionStorage.getItem('roleType'));
      }
    }));

  }

  checkAccess(url: string, userRole){
    if(url.indexOf('slate')!== -1){ return this.checkSlateAccess(userRole)}
    if(url.indexOf('story')!== -1){ return this.checkStoryAccess(userRole)}
    return true;
  }

  checkSlateAccess(userRole){
    return userRole === 'editor' || userRole === 'author' || userRole === 'contributor' ? this.noAccessUrl() : true;
  }

  checkStoryAccess(userRole){
    return userRole === 'contributor' ? this.noAccessUrl() : true;
  }

  noAccessUrl(){
    return this.router.createUrlTree(['/no-access']);
  }

}
