/* tslint:disable:no-string-literal */
import {AfterViewInit, Component, Inject, OnInit, Renderer2} from '@angular/core';
import {AuthService} from './services/auth.service';
import {AuthStatusInterface} from './models/interfaces/auth-status.interface';
import {AuthStatusEnum} from './models/enums/auth-status.enum';
import {NB_WINDOW, NbLayoutRulerService, NbMenuService, NbSidebarService, NbThemeService} from '@nebular/theme';
import {MessagingService} from './services/messaging.service';
import {BehaviorSubject} from 'rxjs';
import {NavigationStart, Router, NavigationEnd} from '@angular/router';
import {filter, map} from 'rxjs/operators';
import {UserService} from './services/user.service';
import {ActiveTabService} from './services/active-tab.service';
import {UserInterface} from './models/interfaces/user.interface';

declare var $: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.sass']
})
export class AppComponent implements OnInit, AfterViewInit {
  title = 'ats';
  authStatus: AuthStatusInterface;
  shouldShowNotification = false;
  loginStatusEnum = AuthStatusEnum;
  UserMenuItems = [];
  isMobile: boolean;
  sideBarIsOpen = false;
  activeTab = 0;
  tabs = [
    {
      index: 0,
      title: 'Dashboard',
      route: 'dashboard',
      active: false,
    },
    {
      index: 1,
      title: 'Slate',
      route: 'slate',
      // route: 'create-pitch',
      active: true,
    },
    {
      index: 2,
      title: 'Published Stories',
      route: 'published-stories',
      // route: 'create-pitch',
      active: false,
    },
  ];
  message = undefined;
  userDetails: UserInterface;
  overlayOpen = false;
  overlayOpenStack = {
    sideBar: false,
    notification: false,
  };
  userRoles = [];
  selectedRole = '';
  roleType: string;
  unredComments = 0;

  // tslint:disable:max-line-length
  constructor(
    private auth: AuthService,
    private nbMenuService: NbMenuService,
    @Inject(NB_WINDOW) private window,
    private messagingService: MessagingService,
    private r2: Renderer2,
    private sidebarService: NbSidebarService,
    private theme: NbThemeService,
    private nbLayoutRulerService: NbLayoutRulerService,
    private router: Router,
    private user: UserService,
    private active: ActiveTabService
  ) {
    // this.tabs.forEach(tab => {
    //   if (tab.active) {
    //     this.router.navigate([tab.route]);
    //   }
    // });

    this.user.userSubject().subscribe(res => {
      this.userDetails = res;
    });
    this.theme.onMediaQueryChange().subscribe(res => {
      this.isMobile = res[0].width <= 768;
    });
    this.auth.getAuthState().subscribe(res => {
      this.authStatus = res;
      if (this.authStatus.status === AuthStatusEnum.loggedIn) {
        this.user.getUser();
        this.user.getUserRole().subscribe(res => {
          this.userRoles = res;
          if (this.user.getCurrentUserRole()) {
            this.selectedRole = this.user.getCurrentUserRole()['title'];
          } else {
            if (this.userRoles && this.userRoles[0]) {
              this.selectedRole = this.userRoles[0].name;
              this.changeRole(this.userRoles[0]);
            }
          }
        });
        const userId = 'user001';
        this.messagingService.requestPermission();
        this.messagingService.currentMessage.subscribe(msg => {
          this.messagingService.getUnreadNotification().subscribe(res => {
            // console.log(res);
            this.message = res['data'].count;
          });
        });
      }
    });
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        if (this.active.findActiveTab(event.urlAfterRedirects) !== undefined) {
          this.activeTab = this.active.findActiveTab(event.urlAfterRedirects);
        } else {
          this.activeTab = undefined;
        }
        // console.log(this.active.findActiveTab(event.urlAfterRedirects));
        // console.log(this.activeTab);
      }
    });
  }

  onMessageRead() {
    this.messagingService.getUnreadNotification().subscribe(res => {
      // console.log(res);
      this.message = res['data'].count;
    });
  }

  ngOnInit(): void {
    this.sidebarService.onExpand().subscribe(expandEvent => {
      this.sideBarIsOpen = true;
    });
    this.sidebarService.onCollapse().subscribe(collapseEvent => {
      this.sideBarIsOpen = false;
    });

  }

  changeRole(role) {
    this.selectedRole = role.name;
    switch (role.name) {
      case 'commisioning_editor':
        this.auth.setRoleType('ce');
        this.user.setUserRole({id: role.id, role: 'chief_editor', title: role.name});
        break;
      case 'publisher':
        this.auth.setRoleType('ae');
        this.user.setUserRole({id: role.id, role: 'chief_editor', title: role.name});
        break;
      case 'chief_editor':
        this.auth.setRoleType('ce');
        this.user.setUserRole({id: role.id, role: 'chief_editor', title: role.name});
        break;
      case 'ats_core':
        this.auth.setRoleType('ats_core');
        this.user.setUserRole({id: role.id, role: 'chief_editor', title: role.name});
        break;
      case 'contributor':
        this.auth.setRoleType('contributor');
        this.user.setUserRole({id: role.id, role: 'contributor', title: role.name});
        break;
      case 'editor':
        this.auth.setRoleType('editor');
        this.user.setUserRole({id: role.id, role: 'editor', title: role.name});
        break;
      case 'author':
        this.auth.setRoleType('author');
        this.user.setUserRole({id: role.id, role: 'editor', title: role.name});
        break;
      default:
        this.auth.setRoleType('contributor');
        this.user.setUserRole({id: role.id, role: role.name, title: role.name});
    }
    if (window.location.href.indexOf('no-access') !== -1) {
      this.router.navigate(['dashboard']);
    } else {
      window.location.reload();
    }
  }

  ngAfterViewInit(): void {
    this.nbLayoutRulerService.getDimensions().subscribe(dimensions => {
      this.isMobile = dimensions.clientWidth <= 768;
    });
    // this.nbMenuService.onItemClick().subscribe(item => {
    //   if (item.item['tag'] === 'log_out') {
    //     this.auth.logout();
    //     this.router.navigate(['login']);
    //   } else if (item.item['tag'] === 'profile') {
    //     this.router.navigate(['profile']);
    //   }
    // });
  }

  toggle() {
    if (this.overlayOpenStack.notification) {
      this.shouldShowNotification = false;
      this.overlayOpenStack.notification = false;
    }
    if (this.overlayOpenStack.sideBar) {
      this.sidebarService.collapse('left');
      this.overlayOpenStack.sideBar = false;
    }

    this.r2.setStyle(document.body, 'overflow', 'auto');
    this.r2.setStyle(document.body, 'height', 'auto');
  }

  showNotification() {
    this.shouldShowNotification = !this.shouldShowNotification;
    this.overlayOpenStack.notification = this.shouldShowNotification;
    if (this.shouldShowNotification) {
      this.r2.setStyle(document.body, 'height', '100vh ');
      this.r2.setStyle(document.body, 'overflow', 'hidden');
    } else {
      this.r2.setStyle(document.body, 'overflow', 'auto');
      this.r2.setStyle(document.body, 'height', 'auto');
    }
  }


  openSidebar() {
    if (this.sideBarIsOpen) {
      this.r2.setStyle(document.body, 'overflow', 'hidden');
      this.r2.setStyle(document.body, 'height', '100vh ');
      this.sidebarService.collapse('left');
      this.overlayOpenStack.sideBar = false;
    } else {
      this.sidebarService.expand('left');
      this.overlayOpenStack.sideBar = true;

      this.r2.setStyle(document.body, 'overflow', 'auto');
      this.r2.setStyle(document.body, 'height', 'auto');

    }
  }


  logout() {
    this.auth.logout();
    this.router.navigate(['login']);
  }

  overlayClick() {
    this.sidebarService.collapse('left');
  }

  setActiveTab(tab, $event) {
    $('.indicator').css('display', 'block');
    this.activeTab = tab;
    this.sidebarService.collapse('left');
  }

  calcWidth() {
    return 'calc(' + ((100 / this.tabs.length) * (this.activeTab)) % +'+ 10px)';
  }

  formatRole(role) {
    return role.replace(/\_/g, ' ');
  }

}
