import { Component, OnInit, Input } from '@angular/core';
import { NbDialogRef, NbDialogModule } from '@nebular/theme';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { UserInterface } from '../../models/interfaces/user.interface';
import { UserService } from '../../services/user.service';
import { EnumService } from '../../services/enum.service';
import { SubSink } from 'subsink';
import { StoryService } from '../../services/story.service';
import { ToastService } from '../../services/toast.service';

@Component({
  selector: 'app-invite-team-member',
  templateUrl: './invite-team-member.component.html',
  styleUrls: ['./invite-team-member.component.sass']
})
export class InviteTeamMemberComponent implements OnInit {

  members: FormGroup;
  filteredOptionsEmail = this.user.getUsersByQuery('');
  filteredOptionsName = this.user.getUsersByQuery('');
  private subs = new SubSink();
  activeIndex = 0;
  tempArr: any = [];
  @Input() storyId: any;
  disableButton = true;
  roleArray = [];
  isLoading = true;

  constructor(public dialogRef: NbDialogRef<any>, private user: UserService, public story: StoryService, private toast: ToastService, private enumService: EnumService) {
    this.members = new FormGroup({
      teamMembers: new FormArray([])
    });
    this.subs.sink = this.enumService.getRoles().subscribe(res => {
      // this.roles = res;
      let _this = this;
      res.forEach(element => {
        if (element.display === 'author' || element.display === 'editor' || element.display === 'contributor') {
          _this.roleArray.push({ value: element.id, name: element.display });
        }
      });
      if (this.roleArray.length) {
        this.isLoading = false;
      }
    }, err => {
      this.isLoading = false;
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

  ngOnInit() {
    this.addProp();
    this.subs.sink = this.members.get('teamMembers').valueChanges.pipe().subscribe(res => {
      this.arrayValueChanged(res);
    });

  }

  arrayValueChanged(teamArr) {
    let apiElement = '';
    let _this = this;
    let activeElement = '';
    this.tempArr.forEach(function (element, index) {
      if (teamArr[index] && element.email !== teamArr[index].email) {
        apiElement = teamArr[index].email;
        _this.activeIndex = index;
        activeElement = 'email';
      } else if (teamArr[index] && element.name !== teamArr[index].name) {
        apiElement = teamArr[index].name;
        _this.activeIndex = index;
        activeElement = 'name';
      }
    });
    this.checkValidity();
    if (apiElement && apiElement.length) {
      activeElement === 'name' ? this.filteredOptionsName = this.user.getUsersByQuery(apiElement) : this.filteredOptionsEmail = this.user.getUsersByQuery(apiElement);
    }
    this.tempArr = teamArr;
  }

  closeDialog() {
    this.dialogRef.close();
  }

  get teamMembersArr() {
    return this.members.get('teamMembers') as FormArray;
  }

  userHandle(value: UserInterface) {
    return value.id ? value.name : value;
  }

  addProp(value = { email: '', name: '', role_id: '' }) {
    this.filteredOptionsEmail = this.user.getUsersByQuery('');
    this.filteredOptionsName = this.user.getUsersByQuery('');
    this.teamMembersArr.push(new FormGroup({
      email: new FormControl(value.email, Validators.required),
      name: new FormControl(value.name, Validators.required),
      role_id: new FormControl(value.role_id, Validators.required),
    })
    );
    this.tempArr.push({ email: '', name: '', role_id: '' });
  }

  onSelectionChange(event, type) {
    if ((event && event.name) || (event && event.email)) {

      this.teamMembersArr.controls[this.activeIndex].get('name').setValue(event.name, { emitEvent: false });
      this.teamMembersArr.controls[this.activeIndex].get('email').setValue(event.email, { emitEvent: false });

      this.checkValidity();
    }
  }

  checkValidity() {
    this.members.get('teamMembers').value.forEach(activeArr => {
      if (!activeArr.name || !activeArr.email || !activeArr.role_id) {
        this.disableButton = true;
      } else {
        this.disableButton = false;
      }
    });
  }

  sendInvite() {
    let value = this.members.get('teamMembers').value;
    let arrToSend = [];
    value.forEach(function (element, index) {
      if (element.name && element.email && element.role_id) {
        arrToSend.push({ email: element.email, name: element.name, role_id: element.role_id });
      }
    });
    let payload = {
      inviteuser: arrToSend,
      story_id: this.storyId,
    }
    this.story.inviteTeamMember(payload, this.storyId).subscribe(res => {
      this.toast.showToast('', res, 'success', '');
      this.closeDialog();
    }, err => {
      this.toast.showToast('', err.error.cause, 'danger', '');
    });
  }

}
