import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Constants } from '../utils/utils';
import { HttpResponseInterface } from '../models/interfaces/http-response.interface';
import { StoryInterface } from '../models/interfaces/story.interface';
import { Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class StoryService {

  // headers = new HttpHeaders({
  //   'story' : this.getStoryId() ? this.getStoryId().toString() : '' ,
  //   'role' : this.getUserRole()
  // });

  constructor(private http: HttpClient) {
  }

  setStoryId(id) {
    sessionStorage.setItem('story_id', id);
  }

  getStoryId() {
    return sessionStorage.getItem('story_id');
  }

  getUserRole() {
    let role = JSON.parse(sessionStorage.getItem('user_role'));
    return role && role['id'] ? role['id'].toString() : '';
  }

  createStory(payload: StoryCreateInterface): Observable<StoryResponseInterface> {
    return this.http.post<HttpResponseInterface>(Constants.host + 'createstory', payload).pipe(map(res => res.data));
  }

  createDraft(payload: DraftInterface, storyId, token) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole(),
      'google-token': token
    });
    return this.http.post<HttpResponseInterface>(Constants.host + 'createdraft', payload, { headers: headers }).pipe(map(res => res.data));
  }

  changeStatus(payload: { story_id: number, story_status: 'story' | 'draft' | 'approved' | 'needs_work' }) {
    let headers = new HttpHeaders({
      'story': payload.story_id.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface>(Constants.host + 'changestatus', payload, { headers: headers }).pipe(map(res => res.data));
  }

  changeStoryMilestoneStatus(payload, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface>(Constants.host + 'changemilstonestatus', payload, { headers: headers }).pipe(map(res => res.data));
  }

  getMilestones(query: string, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.get<HttpResponseInterface>(Constants.host + `storymilestonedata/${query}`, { headers: headers }).pipe(map(res => res.data));
  }

  updateStory(payload: StoryCreateInterface, storyId): Observable<StoryResponseInterface> {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface>(Constants.host + 'storyupdate', payload, { headers: headers }).pipe(map(res => res.data));
  }

  updateApprovedStory(payload: ApprovedStoryInterface, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface>(Constants.host + 'storyupdate', payload, { headers: headers }).pipe(map(res => res.data));
  }

  updateMilestones(payload, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface>(Constants.host + 'storymilestone', payload, { headers: headers }).pipe(map(res => res.data));
  }

  getStory(storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.get<HttpResponseInterface<StoryInterface>>(Constants.host + 'storydetails', { headers: headers }).pipe(map(res => res.data));
  }

  getTaskList(storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.get<HttpResponseInterface<StoryInterface>>(Constants.host + 'tasklist', { headers: headers }).pipe(map(res => res.data));
  }

  createTask(payload, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface<StoryInterface>>(Constants.host + 'createtask', payload, { headers: headers }).pipe(map(res => res.data));
  }

  updateTask(payload, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface<StoryInterface>>(Constants.host + 'taskupdate', payload, { headers: headers }).pipe(map(res => res.data));
  }

  getStoryMilestones() {
    return this.http.get(Constants.host + 'milestonelist').pipe(map(res => res['data']));
  }

  getStoryMembers(storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.get<HttpResponseInterface<StoryInterface>>(Constants.host + 'storymembers', { headers: headers }).pipe(map(res => res.data));
  }

  getStoryDrafts(storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.get<HttpResponseInterface<StoryInterface>>(Constants.host + 'draftlist', { headers: headers }).pipe(map(res => res.data));
  }

  getTaskStatusList() {
    return this.http.get(Constants.host + 'taskstatuslist').pipe(map(res => res['data']));
  }

  deleteTask(payload: { task_id: any }, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface<StoryInterface>>(Constants.host + 'deletetask', payload, { headers: headers }).pipe(map(res => res.data));
  }

  changeTaskStatus(payload, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface<StoryInterface>>(Constants.host + 'changetaskstatus', payload, { headers: headers }).pipe(map(res => res.data));
  }

  publishStory(payload, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post<HttpResponseInterface<StoryInterface>>(Constants.host + 'changestorypublishstatus', payload, { headers: headers }).pipe(map(res => res.data));
  }

  getCurrentStoryMilestones(story_id) {
    let headers = new HttpHeaders({
      'story': story_id.toString(),
      'role': this.getUserRole()
    });
    return this.http.get(Constants.host + 'storymilestonelist', { headers: headers }).pipe(map(res => res['data']));
  }

  inviteTeamMember(payload, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post(Constants.host + 'inviteuser', payload, { headers: headers }).pipe(map(res => res['data']));
  }

  inviteMember(payload, storyId) {
    let headers = new HttpHeaders({
      'story': storyId.toString(),
      'role': this.getUserRole()
    });
    return this.http.post(Constants.host + 'invitemember', payload, { headers: headers }).pipe(map(res => res['data']));
  }
}

export interface StoryCreateInterface {
  title: string;
  small_desc: string;
  budget: number;
  estimate_time: number;
  help_desc: string;
  tags: string[];
  user_id: string;
  role_id: string;
  asset_ids: number[];
  format: string;
  byline: string;
  story_id?: string;
}

export interface DraftInterface {
  title: string;
  draft_version: number,
  draft_url: string,
  summary: string
}
export interface ApprovedStoryInterface {
  story_id?: number;
  assign_id?: number[];
  role_id?: number[];
  publish_date?: string;
  story_status?: string;
}

export interface StoryResponseInterface {
  title: string;
  small_desc: string;
  budget: number;
  estimate_time: number;
  help_desc: string;
  tags: string[];
  user_id: string;
  role_id: string;
  asset_ids: number[];
  format: string;
  byline: string;
  story_status: string;
  slug: string;
  updated_at: string;
  created_at: string;
  id: number;
}


