import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Constants } from '../../utils/utils';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-magic-link',
  templateUrl: './magic-link.component.html',
  styleUrls: ['./magic-link.component.sass']
})
export class MagicLinkComponent implements OnInit {
  loading = true;
  text = 'Verifying Magic Link'
  constructor(private route: ActivatedRoute, private http: HttpClient, private router: Router, private auth: AuthService) {
    this.route.queryParams.subscribe(params => {
      console.log(params);
      this.auth.logout();
      let url = `${params.story}/${params.user}/${params.role}?expires=${params.expires}&signature=${params.signature}`
      console.log(url);
      this.http.get(`${Constants.host}inviteurl/${url}`).subscribe(res => {
        console.log(res);
        this.loading = false;
        if (res['data']) {
          this.text = 'Link verified.Redirecting to login '
          setTimeout(() => {
            this.router.navigate(['login']);
          }, 5000);
        }
      }, err => {
        this.loading = false;
        this.text = 'Link verification failed.Redirecting to login '
        setTimeout(() => {
          this.router.navigate(['login']);
        }, 5000);

      })
    })
  }

  ngOnInit() {
  }

}
