import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {MessagingService} from '../../services/messaging.service';
import {forkJoin} from 'rxjs';
import * as moment from 'moment';

declare var $: any;

@Component({
  selector: 'app-notification-component',
  templateUrl: './notification-component.component.html',
  styleUrls: ['./notification-component.component.sass']
})
export class NotificationComponentComponent implements OnInit {
  @Output() messagesRed = new EventEmitter();
  messages = [];
  showingMessages = [];
  pageSize = 20;
  loading = true;
  pageToLoadNext = 2;
  loadingNewPage = false;
  placeholders: any[];

  constructor(private  messageService: MessagingService) {
    this.messageService.listMessages().subscribe(res => {

      this.messages = JSON.parse(JSON.stringify(res.notifications));
      const messagesCopy = JSON.parse(JSON.stringify(res.notifications));
      this.showingMessages = messagesCopy.splice(0, this.pageSize);
      let calls = [];
      this.showingMessages.forEach(message => {
        calls.push(message.id);
      });
      this.messageService.markaAsRead({notifications: calls}).subscribe(res => {
        this.messagesRed.emit(true);
      });
      this.loading = false;
    });
    this.messageService.currentMessage.subscribe(mesage => {
      this.messages = [mesage, [...this.messages]];
      this.showingMessages = [mesage, [...this.showingMessages]];
    });
  }

  ngOnInit() {
  }

  // onScroll() {
  //   console.log('e');
  // }

  loadNext() {
    if (this.loadingNewPage) {
      return;
    }
    this.loadingNewPage = true;
    console.log(this.messages);
    if(this.messages.length){
    let calls = [];
    calls = this.showingMessages.map(message => {
      return message.id;
    });
    this.messageService.markaAsRead({notifications: calls}).subscribe(res => {
      this.messagesRed.emit(true);
    });

    }
    const messagesCopy = JSON.parse(JSON.stringify(this.messages));
    // console.log(messagesCopy);
    // console.log(this.pageToLoadNext * this.pageSize);
    this.showingMessages = messagesCopy.splice(0, this.pageToLoadNext * this.pageSize);
    // console.log(this.showingMessages);
    this.pageToLoadNext++;
    setTimeout(() => {
      this.loadingNewPage = false;
    }, 200);
  }

  detectStoryName(message): { placesToUnderline: any[], placesToBold: any[], msg: any[] } {
    let Bold = [];
    let Underline = [];
    let msgArr = [];
    if (message && message.data) {

      let msg = message.data.message as string;
      // console.log(message);
      if (message.data.message.includes(message.data.create_by.name)) {
        msgArr = msg.split(message.data.create_by.name);
        // console.log(msgArr);
        msgArr[0] = message.data.create_by.name;
        Bold.push(0);
      }
      if (message.data.message.includes(message.story_title)) {
        if (msgArr[1]) {
          msgArr[1] = msgArr[1].split(message.story_title)[0];
        } else {
          msgArr[1] = message.data.message.split(message.story_title)[0];
        }
        msgArr[2] = message.story_title;
        Bold.push(2);
        Underline.push(2);
      }
    }
    return {placesToUnderline: Underline, placesToBold: Bold, msg: msgArr};
  }

  redirectionURL(message) {
    if (message.data.actionable_type === 'story' || message.data.actionable_type === 'task' || message.data.actionable_type === 'App\\Models\\Story') {
      window.location.href = window.location.origin + '/story/' + message.data.story_id;
    } else {
      window.location.href = window.location.origin + '/slate/' + message.data.story_id;
    }
  }

  dateFromMiliseconds(t) {
    let date = moment(t);
    if (moment().diff(date, 'days') >= 1) {
      return date.fromNow(); // '2 days ago' etc.
    } else if (moment().diff(date, 'days') >= 30) {
      return date.format('DD/MM/YYYY');
    } else {
      return date.calendar(null, {
        lastDay: '[Yesterday]',
        sameDay: 'hh:mm A',
      });
    }
  }
}
