import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {DropDown} from '../models/classes/drop-down';
import {Observable, Subject} from 'rxjs';
import {Constants} from '../utils/utils';
import {HttpResponseInterface} from '../models/interfaces/http-response.interface';

@Injectable({
  providedIn: 'root'
})
export class EnumService {

  constructor(private http: HttpClient) {
  }

  getRoles(): Observable<DropDown[]> {
    const sub = new Subject<DropDown[]>();
    this.http.get<HttpResponseInterface>(Constants.host + 'rolelist').subscribe(res => {
      sub.next(res.data.map(entry => new DropDown(entry.id, entry.role_title)));
      sub.complete();
    });
    return sub;
  }

  getFormatList(): Observable<DropDown[]> {
    const sub = new Subject<DropDown[]>();
    this.http.get<HttpResponseInterface>(Constants.host + 'formatlist').subscribe(res => {
      sub.next(Object.keys(res.data).map(entry => new DropDown(entry, res.data[entry])));
      sub.complete();
    });
    return sub;
  }
}
