import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {LoginPageComponent} from './pages/login-page/login-page.component';
import {DashboardPageComponent} from './pages/dashboard-page/dashboard-page.component';
import {AuthGardService} from './services/auth-gard.service';
import {ProfilePageComponent} from './pages/profile-page/profile-page.component';
import {CreatePitchComponent} from './pages/create-pitch/create-pitch.component';
import {SlateListComponent} from './pages/slate-list/slate-list.component';
import {CeSlateDetailsComponent} from './pages/ce-slate-details/ce-slate-details.component';
import {CeApproveSlateComponent} from './pages/ce-approve-slate/ce-approve-slate.component';
import {PublishedStoriesComponent} from './pages/published-stories/published-stories.component';
import {StoryWorkspaceComponent} from './pages/story-workspace/story-workspace.component';
import {NoAccessComponent} from './pages/no-access/no-access.component';
import {MagicLinkComponent} from './pages/magic-link/magic-link.component';


const routes: Routes = [
  {path: '', pathMatch: 'full', redirectTo: 'dashboard'},
  {
    path: 'login', component: LoginPageComponent,
  },
  {
    path: 'magic-link', component: MagicLinkComponent,
  },
  {
    path: 'dashboard', component: DashboardPageComponent, canActivate: [AuthGardService]
  }, {
    path: 'profile', component: ProfilePageComponent, canActivate: [AuthGardService]
  }, {
    path: 'create-pitch', component: CreatePitchComponent, canActivate: [AuthGardService]
  }, {
    path: 'edit-pitch/:id', component: CreatePitchComponent, canActivate: [AuthGardService]
  },
  {
    path: 'edit-story/:id', component: CreatePitchComponent, canActivate: [AuthGardService]
  }, {
    path: 'slate', component: SlateListComponent, canActivate: [AuthGardService]
  }, {
    path: 'slate/:id', component: CeSlateDetailsComponent, canActivate: [AuthGardService]
  }, {
    path: 'approve-slate/:id', component: CeSlateDetailsComponent, canActivate: [AuthGardService]
  }, {
    path: 'story/:id', component: StoryWorkspaceComponent, canActivate: [AuthGardService]
  }, {
    path: 'published-stories', component: PublishedStoriesComponent, canActivate: [AuthGardService]
  }, {
    path: 'no-access', component: NoAccessComponent
  }, {
    path: '**',
    redirectTo: 'dashboard'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
