import { Injectable } from '@angular/core';
import { NbToastrService, NbComponentStatus } from '@nebular/theme';

@Injectable({
  providedIn: 'root'
})
export class ToastService {

  constructor(public toastr: NbToastrService) { }

  showToast(trailingcontent, message, status: NbComponentStatus, icon) {
    this.toastr.show(trailingcontent, message ? message : 'Error Occured', { status, icon: icon });
  }

}
