import { Component, OnInit, Input, SimpleChanges, OnChanges } from '@angular/core';
import { StoryInterface } from '../../models/interfaces/story.interface';
import { MilestoneInterface } from '../../models/interfaces/milestone.interface';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'app-dash-story-card',
  templateUrl: './dash-story-card.component.html',
  styleUrls: ['./dash-story-card.component.sass']
})
export class DashStoryCardComponent implements OnInit, OnChanges {
  @Input() data: StoryInterface;
  @Input() type: string;
  numberOfMilestonesCollapsed: number;
  isMilestonesCollapsed: string;
  isEditor: boolean;
  isCE: boolean;
  isContributor: boolean;
  isProfile: boolean;
  created_dates = [];
  completedMilestones: MilestoneInterface[];
  tags = false;
  constructor() {
  }

  ngOnInit() {

    this.isEditor = this.type === 'editor';
    this.isCE = this.type === 'chief_editor';
    this.isContributor = this.type === 'contributor';
    this.isProfile = this.type === 'profile';

    if (this.data.milestone && this.data.milestone.data.length) {
      this.numberOfMilestonesCollapsed = (this.data.milestone.data.filter(value =>
        value.milestone_status !== 'completed' && Date.parse(value.status_deadline) < Date.now()
      )).length;
      this.isMilestonesCollapsed = this.numberOfMilestonesCollapsed ? 'Not ' : '';
      this.findCompletedMilestones();
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.data) {
      this.data = changes.data.currentValue;
      if (this.data.tags) {
        this.tags = true;
      }
    }
  }



  dueDatePassed(item) {
    return Date.parse(item.status_deadline) < Date.now();
  }

  formatMilestone(milestone) {
    let temp = milestone.split('_');
    return (temp.filter(val => val[0].toUpperCase())).join(' ');
  }

  findCompletedMilestones() {
    this.completedMilestones = this.data.milestone.data.filter(value => value.milestone_status === 'completed');
    return (this.completedMilestones).length;
  }

  chooseFormatIcon() {
    switch (this.data.format) {
      case 'Video Essay':
        return 'activity-outline';
      case 'Photo Essay':
        return 'image-outline';
      default:
        return 'file-outline';
    }
  }

}
