import {Injectable} from '@angular/core';
// import {AngularFireDatabase} from '@angular/fire/database';
// import {AngularFireAuth} from '@angular/fire/auth';
import {AngularFireMessaging} from '@angular/fire/messaging';
import {map, mergeMapTo} from 'rxjs/operators';
import {take} from 'rxjs/operators';
import {BehaviorSubject} from 'rxjs';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Constants} from '../utils/utils';
import {HttpResponseInterface} from '../models/interfaces/http-response.interface';

@Injectable()
export class MessagingService {

  currentMessage = new BehaviorSubject(null);

  constructor(
    // private angularFireDB: AngularFireDatabase,
    // private angularFireAuth: AngularFireAuth,
    private angularFireMessaging: AngularFireMessaging, private http: HttpClient) {
    this.angularFireMessaging.messaging.subscribe(
      (_messaging) => {
        _messaging.onMessage = _messaging.onMessage.bind(_messaging);
        _messaging.onTokenRefresh = _messaging.onTokenRefresh.bind(_messaging);
        _messaging.usePublicVapidKey('BGEOg7c9R-yh1EMZxktBGGPuqmBpUdI4QKoRX3IrMQegBBlMWKCxsO8jbC_qjgD-sM5zfo7_E5FFO3pZTBR6fn8');
      }
    );
  }

  /**
   * update token in firebase database
   *
   * @param userId userId as a key
   * @param token token as a value
   */
  updateToken(token) {
    this.http.post(`${Constants.host}registerfcm`, {token: token}).subscribe(res => {
      console.log(res);
    });
  }

  getUnreadNotification() {
    return this.http.get(`${Constants.host}unreadnotifincation`,);
  }

  listMessages() {
    let headers = new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded'
    });
    return this.http.get<HttpResponseInterface>(Constants.host + 'notificationlist', {headers: headers}).pipe(map(res => res.data));
  }

  markaAsRead(payload) {
    return this.http.post<HttpResponseInterface>(Constants.host + 'readnotification', payload,).pipe(map(res => res.data));
  }

  /**
   * request permission for notification from firebase cloud messaging
   *
   * @param userId userId
   */

  requestPermission() {
    this.angularFireMessaging.requestToken.subscribe(
      (token) => {
        console.log(token);
        this.updateToken(token);
        this.listenToMessages();
      },
      (err) => {
        console.error('Unable to get permission to notify.', err);
      }
    );
  }

  listenToMessages() {
    this.angularFireMessaging.messages.subscribe(
      (payload) => {
        console.log(payload);
        this.currentMessage.next(payload);
      });
  }

  subscribeToMessages() {
    return this.angularFireMessaging.messages;
  }


}
